import { Tabs } from 'antd'
import { connect } from 'dva'
import * as PropTypes from 'prop-types'
import React from 'react'
import Tour from 'reactour'
import localStore from 'storejs'
import CourseList from '../../components/CourseList'
import RankList from '../../components/RankList'
import steps from '../../fixtures/steps'
import TwoCol from '../../routers/TwoCol'

import './index.less'

const { TabPane } = Tabs

const mapStateToProps = ({ home }) => ({
  courses: home.courses,
  teachers: home.teachers,
})

export class HomePage extends React.Component {
  state = {
    showTour: false,
  }
  componentDidMount() {
    const userInfo = localStore.get('userInfo') || {}
    if (userInfo.type === 1) {
      const notFirstForTalent = localStore.get('notFirstForTalent')
      if (!notFirstForTalent) {
        localStore.set('notFirstForTalent', true)
        this.setState({ showTour: true })
      }
    }
  }
  render() {
    const { dispatch, courses, teachers } = this.props
    const { showTour } = this.state

    return (
      <>
        <TwoCol
          left={
            <Tabs
              defaultActiveKey="latest"
              onChange={key =>
                dispatch({
                  type: 'home/fetch',
                  payload: {
                    type: key,
                  },
                })
              }
            >
              <TabPane tab="最新认证" key="latest">
                {
                  <CourseList
                    courses={courses}
                    dispatch={dispatch}
                    tab="latest"
                  />
                }
              </TabPane>
              <TabPane tab="热门认证" key="popular">
                {
                  <CourseList
                    courses={courses}
                    dispatch={dispatch}
                    tab="popular"
                  />
                }
              </TabPane>
            </Tabs>
          }
          right={
            <RankList title="热门企业" type="teacher" dataSource={teachers} />
          }
        />
        <Tour
          steps={steps.talent}
          isOpen={showTour}
          onRequestClose={() => this.setState({ showTour: false })}
        />
      </>
    )
  }
}

HomePage.propTypes = {
  dispatch: PropTypes.any,
  courses: PropTypes.any,
  teachers: PropTypes.any,
}

export default connect(mapStateToProps)(HomePage)
