import { Avatar, List, Skeleton } from 'antd'
import React from 'react'
import { Link } from 'dva/router'
import styled from 'styled-components'
import './RankList.less'

export default function RankList({ title, type = 'teacher', dataSource }) {
  return (
    <List
      header={<div>{title}</div>}
      footer={
        <Center>
          <Link to={`/${type}s`} data-testid="view-more">
            查看更多
          </Link>
        </Center>
      }
      itemLayout="horizontal"
      dataSource={dataSource}
      renderItem={item => {
        return (
          <List.Item data-testid={`${type}-item`}>
            <Skeleton avatar title={false} loading={false} active>
              <List.Item.Meta avatar={<Avatar src={item.avatar} />} />
              <div>
                <div className="name">
                  <Link to={`/company/detail/${item.id || item.objectId}`}>
                    {item.name || item.nickname}
                  </Link>
                </div>
                <span> 认证 {item.qualification.length}, </span>
                <span> 课程 {item.course_count}</span>
                <div>{item.position}</div>
              </div>
            </Skeleton>
          </List.Item>
        )
      }}
    />
  )
}

const Center = styled.div`
  text-align: center;
`
