import { Alert, Form, Input, Modal, Tabs, notification } from 'antd'
import { connect } from 'dva'
import { routerRedux, Link } from 'dva/router'
import React, { useState } from 'react'
import Editor from '../components/editor'
import 'braft-editor/dist/index.css'
const { TabPane } = Tabs

const courseFields = [
  { name: '认证名称', field: 'title' },
  { name: '认证描述', field: 'description' },
  { name: '认证分类', field: 'category' },
  { name: '开始时间', field: 'start_time' },
  { name: '结束时间', field: 'end_time' },
  { name: '人数限制', field: 'student_limit' },
  // { name: '收费标准', field: 'expense' },
]

const CourseModal = connect(({ course }) => ({ course }))(
  Form.create()(props => {
    const [htmlContent, setHtmlContent] = useState('')
    const [courseType, setCourseType] = useState(0)
    const {
      course: { error, modalVisible },
      form,
      dispatch,
    } = props
    const { getFieldDecorator } = form

    const onCancel = () => {
      dispatch({ type: 'course/hideModal' })
      window.location.href = '/company/courses'
    }

    const onSubmit = e => {
      e.preventDefault()
      form.validateFields(async (err, values) => {
        if (err) {
          return
        }
        if (courseType === 0) {
          await dispatch({
            type: 'course/out_create',
            payload: {
              content: htmlContent,
              ...values,
            },
          })
          notification.open({
            title: '通知',
            content: (
              <p>
                课程发布记录已存储至区块链，详情点击
                <Link target="_blank" to="/block/5de33a987a49090009fa9c27">
                  查看
                </Link>
              </p>
            ),
          })
          window.location.href = '/company/courses'
        } else if (courseType === 1) {
          await dispatch({
            type: 'course/create',
            payload: values,
          }).then(data => {
            dispatch({ type: 'course/hideModal' })
            dispatch({ type: 'course/showContractModal' })
            dispatch(routerRedux.push(`/company/edit-course/${data.id}`))
          })
        }
      })
    }

    const editorChange = value => {
      console.log(value)
      setHtmlContent(value)
    }

    return (
      <Modal
        width={'54vw'}
        visible={modalVisible}
        title="新开认证"
        maskClosable={false}
        onCancel={onCancel}
        onOk={onSubmit}
        cancelText="取消"
        okText={courseType === 0 ? '确认' : '下一步'}
        destroyOnClose
      >
        <Tabs
          defaultActiveKey={0}
          onChange={activeKey => setCourseType(+activeKey)}
        >
          <TabPane tab="我有自己的课程需要认证" key={0}>
            {courseType === 0 && (
              <Form>
                {error && error.is && (
                  <Alert
                    style={{ marginTop: -15, marginBottom: 5 }}
                    message={error.message}
                    type="error"
                    showIcon
                  />
                )}
                <Form.Item label={'认证标题'}>
                  {getFieldDecorator('title', {
                    rules: [{ required: true, message: `认证标题不能为空` }],
                  })(<Input placeholder={`请输入认证标题`} />)}
                </Form.Item>
                <Form.Item label={'认证描述'}>
                  {getFieldDecorator('description', {
                    rules: [{ required: true, message: `认证描述不能为空` }],
                  })(<Input placeholder={`请输入认证描述`} />)}
                </Form.Item>
                <Form.Item label={'课程链接'}>
                  {getFieldDecorator('course_link', {
                    rules: [
                      { required: true, message: `课程链接不能为空` },
                      { type: 'url', message: `请输入正确的网址` },
                    ],
                  })(<Input placeholder={`请输入课程链接`} />)}
                </Form.Item>
                <Form.Item label={'认证链接'}>
                  {getFieldDecorator('auth_link', {
                    rules: [
                      { required: true, message: `认证链接不能为空` },
                      { type: 'url', message: `请输入正确的网址` },
                    ],
                  })(<Input placeholder={`请输入认证链接`} />)}
                </Form.Item>
                <Form.Item label={'认证内容'}>
                  <Editor onChange={editorChange} />
                </Form.Item>
              </Form>
            )}
          </TabPane>
          <TabPane tab="使用平台开课认证" key={1}>
            {courseType === 1 && (
              <Form>
                {error && error.is && (
                  <Alert
                    style={{ marginTop: -15, marginBottom: 5 }}
                    message={error.message}
                    type="error"
                    showIcon
                  />
                )}
                {courseFields.map(item => (
                  <Form.Item label={item.name} key={item.field}>
                    {getFieldDecorator(item.field, {
                      rules: [
                        { required: true, message: `${item.name}不能为空` },
                      ],
                    })(
                      <Input
                        placeholder={`请输入${item.name}...`}
                        data-testid={item.name === '认证名称' && 'course-title'}
                      />
                    )}
                  </Form.Item>
                ))}
                <Form.Item label={'收费标准'}>
                  {getFieldDecorator('expense', {
                    rules: [{ required: true, message: `收费标准不能为空` }],
                  })(
                    <Input
                      prefix="￥"
                      suffix="RMB"
                      placeholder={`请输入收费标准...`}
                    />
                  )}
                </Form.Item>
              </Form>
            )}
          </TabPane>
        </Tabs>
      </Modal>
    )
  })
)

export default CourseModal
