import { map, reverse } from 'lodash'
import { getFakeProgress } from '../fixtures/course'
import { format } from '../utils/datetime'
import { getCommentsByLessonId } from './comment'
import { getLikesByLessonId } from './like'
import { notify } from './notification'
import { getTasksByLessonId } from './task'
import { track } from './tracking'
import {
  award,
  getUserName,
  extractSimpleFieldsForUser,
  getSimpleUserInfoById,
} from './user'
import AV from './vendor/av-client'

export const out_create = async ({
  title,
  description,
  content,
  course_link,
  auth_link,
  creator,
}) => {
  const user = AV.Object.createWithoutData('users', creator)
  const QualificationObject = AV.Object.extend('qualifications')
  const qualification = new QualificationObject()

  qualification.set('creator', user)
  qualification.set('exam', auth_link)
  qualification.set('name', title)

  const savedQualification = await qualification.save()
  console.log('创建认证完成')

  const LessonClass = AV.Object.extend('lessons')
  const lesson = new LessonClass()
  lesson.set('creator', creator)
  lesson.set('title', title)
  lesson.set('description', description)
  lesson.set('start_time', new Date())
  lesson.set('end_time', new Date())
  lesson.set('content', content)
  lesson.set('url', course_link)
  lesson.set('qualification', savedQualification)

  const saved = await lesson.save()
  console.log('创建课程完成')

  user.increment('taught_count', 1)
  await user.save()
  console.log('同步taught count完成')

  return saved.id
}

export const create = async ({
  title,
  category,
  start_time,
  end_time,
  student_limit,
  expense,
  creator,
  avatar,
}) => {
  const LessonClass = AV.Object.extend('lessons')
  const lesson = new LessonClass()

  lesson.set('creator', creator)
  lesson.set('title', title)
  lesson.set('category', category)
  lesson.set('start_time', new Date(start_time))
  lesson.set('end_time', new Date(end_time))
  lesson.set('student_limit', +student_limit)
  lesson.set('expense', expense)
  lesson.set('avatar', avatar)

  const response = await lesson.save()

  console.log('课程保存成功，开始同步taught count')
  const user = new AV.Object.createWithoutData('users', creator)
  user.increment('taught_count', 1)
  user.save().then(updated => {
    console.log('同步taught count完成')
  })
  return response
}

export const fetch = async ({ lessonId }) => {
  const query = new AV.Query('lessons')
  const lesson = await query.get(lessonId)
  return await extractFieldsForLesson(lesson)
}

export const buy = async ({ userId, lessonId }) => {
  const participationClass = AV.Object.extend('participations')
  const participation = new participationClass()

  participation.set('lesson', AV.Object.createWithoutData('lessons', lessonId))
  participation.set('user', AV.Object.createWithoutData('users', userId))

  await participation.save()
  console.log('购买课程成功')
  const lesson = new AV.Object.createWithoutData('lessons', lessonId)
  lesson.increment('members_count', 1)
  await lesson.save()
  console.log('课程学员数量更新成功')
}

export const publish = async ({ lessonId, title }) => {
  const lesson = new AV.Object.createWithoutData('lessons', lessonId)
  lesson.set('published', true)
  lesson.set('title', title)
  return await lesson.save()
}

export const update = async ({ lessonId, ...props }) => {
  const lesson = new AV.Object.createWithoutData('lessons', lessonId)
  map(props, (v, k) => {
    lesson.set(k, v)
  })
  return await lesson.save()
}

export const like = async ({
  lessonId,
  likes,
  author,
  currentUser,
  title,
  hash,
  abilityName,
}) => {
  const lesson = new AV.Object.createWithoutData('lessons', lessonId)
  lesson.set('likes', [...likes, currentUser])
  await lesson.save()

  award({
    userId: author,
    abilityName: abilityName,
    stats: 1,
  })

  notify({
    content: `用户 ${currentUser} 认可了您的《${title}》，您获得15个「${abilityName}」能力点提升`,
    from: currentUser,
    to: author,
  })

  track({
    hash,
    userId: author,
    content: `用户 ${currentUser} 为 ${author} 的《${title}》进行认可, ${author} 获得15个「${abilityName}」能力点提升`,
  })
}

export async function listMembers({ lessonId }) {
  const lesson = AV.Object.createWithoutData('lessons', lessonId)
  const query = new AV.Query('participations')
  query.equalTo('lesson', lesson)

  const querySet = await query.find()
  return await Promise.all(
    querySet.map(participation =>
      getSimpleUserInfoById({ id: participation.get('user').id })
    )
  )
}

export async function listComments({ lessonId }) {
  return getCommentsByLessonId({ lessonId: lessonId })
}

export async function listLikes({ lessonId }) {
  return getLikesByLessonId({ lessonId: lessonId })
}

export async function listTasks({ lessonId }) {
  return getTasksByLessonId({ lessonId: lessonId })
}

export async function listLearnt({ user }) {
  const query = new AV.Query('participations')
  query.equalTo('user', AV.Object.createWithoutData('users', user))

  const participations = await query.find()
  return await Promise.all(
    participations
      .map(participation => participation.get('lesson').id)
      .map(id => fetch({ lessonId: id }))
  )
}

export async function listTaught({ user }) {
  let query = new AV.Query('lessons')
  query.equalTo('creator', user)
  const lessons = await query.find()

  return await Promise.all(reverse(lessons).map(extractFieldsForLesson))
}

export async function getTaughtCount({ userId }) {
  let query = new AV.Query('lessons')
  query.equalTo('creator', userId)
  return await query.count()
}

async function extractFieldsForLesson(lesson) {
  const questions = lesson.get('questions') || []
  const questions_count = questions.length + 1
  const answers_count = questions.reduce(
    (prev, current) => prev + (current.answers || []).length,
    0
  )
  const likes = lesson.get('likes') || []
  let query = new AV.Query('users')
  const user = await query.get(lesson.get('creator'))
  const creator = user.toFullJSON()
  return {
    id: lesson.id,
    creator: await getUserName(lesson.get('creator')),
    creatorObj: creator,
    title: lesson.get('title'),
    description: lesson.get('description'),
    auth_link: lesson.get('auth_link'),
    course_link: lesson.get('course_link'),
    category: lesson.get('category'),
    start_time: format(lesson.get('start_time'), 'YYYY年MM月DD日'),
    end_time: format(lesson.get('end_time'), 'YYYY年MM月DD日'),
    published: lesson.get('published'),
    expense: lesson.get('expense'),
    comments: lesson.get('comments') || [],
    questions: questions,
    likes: lesson.get('likes') || [],
    tasks: lesson.get('tasks') || [],
    sections: lesson.get('sections') || [],
    student_limit: +lesson.get('student_limit'),
    teaching_progress:
      lesson.get('teaching_progress') ||
      getFakeProgress(
        +lesson.get('members_count'),
        +lesson.get('student_limit')
      ),
    qualified: lesson.get('qualified'),
    comments_count: questions_count + answers_count,
    questions_count: questions_count,
    answers_count: answers_count,
    likes_count: likes.length,
    members_count: lesson.get('members_count') || 0,
    avatar: lesson.get('avatar'),
  }
}
