import React, { Component } from 'react'
import { connect } from 'dva'
import { Divider, Radio } from 'antd'
import TwoCol from '../../routers/TwoCol'
import User from './User'

class Filter extends Component {
  constructor(props) {
    super(props)
    this.state = {
      custPosition: '',
    }
  }

  onChange(type, e) {
    const { dispatch } = this.props
    this.setState({
      [`${type}`]: e.target.value,
    })
    if (type === 'position') {
      this.setState({
        custPosition: e.target.value,
      })
    }
    dispatch({
      type: 'company/shuffle',
    })
  }

  render() {
    const { companyUser = {} } = this.props.company
    const { users = [] } = companyUser
    return (
      <TwoCol
        inverse
        left={
          <div className="position_subscription">
            <h3>职位订阅</h3>
            <Divider />
            <div>
              <p>热门城市</p>
              <Radio.Group
                onChange={e => this.onChange('city', e)}
                value={this.state.city}
              >
                <Radio value={1}>成都</Radio>
                <Radio value={2}>福州</Radio>
                <Radio value={3}>上海</Radio>
                <Radio value={4}>厦门</Radio>
                <Radio value={5}>北京</Radio>
                <Radio value={6}>广州</Radio>
                <Radio value={8}>深圳</Radio>
                <a style={{ marginLeft: '23px' }} href="javascript:;">
                  更多
                </a>
              </Radio.Group>
            </div>
            <Divider />
            <div>
              <p>职位</p>
              <Radio.Group
                onChange={e => this.onChange('position', e)}
                value={this.state.position}
              >
                <Radio value={'后端'}>后端</Radio>
                <Radio value={'前端'}>前端</Radio>
                <Radio value={'Android'}>Android</Radio>
                <Radio value={'iOS'}>iOS</Radio>
                <Radio value={'人工智能'}>人工智能</Radio>
                <Radio value={'AI'}>AI</Radio>
                <Radio value={'区块链'}>区块链</Radio>
                <Radio value={'大数据'}>大数据</Radio>
                <a style={{ marginLeft: '23px' }} href="javascript:;">
                  更多
                </a>
              </Radio.Group>
            </div>
          </div>
        }
        right={
          <div>
            {users.map((elem, index) => {
              return (
                <User
                  key={index}
                  data={elem}
                  custPosition={this.state.custPosition}
                />
              )
            })}
          </div>
        }
      />
    )
  }
}

export default connect(({ company }) => ({
  company,
}))(Filter)
