import { isEmpty } from 'lodash'
import localStore from 'storejs'

import { getFakeBlockList } from '../fixtures/profile'
import AV from './vendor/av-client'
import { format } from '../utils/datetime'

export async function getActionsById({ userId }) {
  const query = new AV.Query('action_tracking')
  query.equalTo('user', userId)
  query.descending('createdAt')

  const actions = await query.find()
  const profileActions = actions.map(extractFieldsForAction)
  const userInfo = localStore.get('userInfo') || {}
  return isEmpty(profileActions)
    ? getFakeBlockList(userInfo.nickname)
    : profileActions
}

export async function track({ userId, content, hash }) {
  const actionClass = AV.Object.extend('action_tracking')
  const action = new actionClass()

  action.set('user', userId)
  action.set('content', content)
  hash && action.set('hash', hash)

  action.save().then(saved => {
    console.log('记录已经被保存至账本')
  })
}

function extractFieldsForAction(action) {
  return {
    hash: action.get('hash'),
    time: format(action.get('createdAt'), 'YYYY年MM月DD日 hh:mm:ss'),
    content: action.get('content'),
    href:
      action.get('href') &&
      action.get('href').replace('http://localhost:3000/reachain-web', ''),
  }
}
