import { Button, Upload, Form, Input, Radio, Icon, message } from 'antd'
import { Link } from 'dva/router'
import { updateCompany } from '../../services/user'
import React from 'react'
import logoImg from '../../assets/logo.png'
import './auth.less'

class RegistrationForm extends React.Component {
  state = { type: 'company' }
  handleSubmit = e => {
    e.preventDefault()
    this.props.form.validateFields(async (err, values) => {
      if (!err) {
        await updateCompany({
          nickname: values.nickname,
          avatar: values.avatar,
          description: values.description,
          userId: this.props.location.search.substring(9),
        })
        message.success('注册成功，即将跳转到登录页')
        setTimeout(() => {
          window.location.href = '/login'
        }, 2000)
      }
    })
  }
  handleFormLayoutChange = e => {
    this.setState({ type: e.target.value })
  }
  render() {
    const { getFieldDecorator } = this.props.form
    const { type } = this.state
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
      },
    }
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0,
        },
        sm: {
          span: 16,
          offset: 8,
        },
      },
    }

    return (
      <div className="register_page">
        <img className="login_logo" src={logoImg} alt="" />
        <Form
          {...formItemLayout}
          onSubmit={this.handleSubmit}
          className="register_form"
        >
          <Form.Item label={<span>公司名称&nbsp;</span>}>
            {getFieldDecorator('nickname', {
              rules: [
                { required: true, message: '输入公司名!', whitespace: true },
              ],
            })(<Input />)}
          </Form.Item>
          <Form.Item label={<span>公司Logo&nbsp;</span>}>
            {getFieldDecorator('avatar', {
              rules: [
                {
                  required: true,
                  message: '输入公司Logo链接!',
                  whitespace: true,
                },
                { type: 'url', message: '请输入正确链接' },
              ],
            })(<Input />)}
          </Form.Item>
          <Form.Item label={<span>公司简介&nbsp;</span>}>
            {getFieldDecorator('description', {
              rules: [
                { required: true, message: '输入公司简介!', whitespace: true },
              ],
            })(<Input />)}
          </Form.Item>
          <Form.Item label="进驻类型" {...formItemLayout}>
            <Radio.Group
              defaultValue="company"
              onChange={this.handleFormLayoutChange}
            >
              <Radio.Button value="company">企业</Radio.Button>
              <Radio.Button value="personal">个人</Radio.Button>
            </Radio.Group>
          </Form.Item>
          {type === 'company' ? (
            <>
              <Form.Item label="上传营业执照">
                {getFieldDecorator('upload', {
                  valuePropName: 'license',
                  rules: [
                    {
                      required: true,
                      message: '请上传营业执照!',
                    },
                  ],
                })(
                  <Upload
                    name="logo"
                    action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                    listType="picture"
                  >
                    <Button>
                      <Icon type="upload" /> 点击上传
                    </Button>
                  </Upload>
                )}
              </Form.Item>
              <Form.Item label="上传法人身份证">
                {getFieldDecorator('upload', {
                  valuePropName: 'userID',
                  rules: [
                    {
                      required: true,
                      message: '请上传法人身份证!',
                    },
                  ],
                })(
                  <Upload
                    name="logo"
                    action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                    listType="picture"
                  >
                    <Button>
                      <Icon type="upload" /> 点击上传
                    </Button>
                  </Upload>
                )}
              </Form.Item>
            </>
          ) : (
            <Form.Item label="上传本人身份证">
              {getFieldDecorator('upload', {
                valuePropName: 'userID',
                rules: [
                  {
                    required: true,
                    message: '请上传本人身份证!',
                  },
                ],
              })(
                <Upload
                  name="logo"
                  action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                  listType="picture"
                >
                  <Button>
                    <Icon type="upload" /> 点击上传
                  </Button>
                </Upload>
              )}
            </Form.Item>
          )}
          <Form.Item {...tailFormItemLayout}>
            <Button
              type="primary"
              // htmlType="submit"
              className="register-form-button"
              onClick={this.handleSubmit}
            >
              注册
            </Button>
            Or <Link to="/login">已有账号</Link>
          </Form.Item>
        </Form>
      </div>
    )
  }
}

export default Form.create({ name: 'register' })(RegistrationForm)
