import { message, notification } from 'antd'
import pathToRegexp from 'path-to-regexp'
import localStore from 'storejs'
import { getFakeHash } from '../fixtures/block'
import { getFakeImage } from '../fixtures/course'
import { getFakeAbility } from '../fixtures/profile'
import * as lesson from '../services/lesson'
import { notify } from '../services/notification'

let userInfo = localStore.get('userInfo') || {}

export default {
  namespace: 'course',
  state: {
    list: [],
    editing: {
      sections: [],
      members: [],
      comments: [],
      questions: [],
      likes: [],
      tasks: [],
      title: '',
    },
    error: {},
    modalVisible: false,
    contractModalVisible: false,
  },
  effects: {
    *out_create({ payload }, { call, put }) {
      const { id } = localStore.get('userInfo') || {}
      const response = yield call(lesson.out_create, {
        ...payload,
        creator: id,
        avatar: getFakeImage(),
      })
      if (response) {
        yield put({ type: 'reload' })
        notification.success({
          message: '认证课程创建成功',
        })
        return response
      } else {
        yield put({
          type: 'setError',
          payload: {
            is: true,
            message: response,
          },
        })
      }
    },
    *create({ payload }, { call, put }) {
      const { id } = localStore.get('userInfo') || {}
      const response = yield call(lesson.create, {
        ...payload,
        creator: id,
        avatar: getFakeImage(),
      })

      if (response.createdAt) {
        yield put({ type: 'reload' })
        notification.success({
          message: '认证课程创建成功',
        })
        return response
      } else {
        yield put({
          type: 'setError',
          payload: {
            is: true,
            message: response,
          },
        })
      }
    },
    *fetch({ payload }, { call, put }) {
      if (payload.id) {
        const course = yield call(lesson.fetch, { lessonId: payload.id })

        yield put({
          type: 'save',
          payload: {
            editing: course,
          },
        })
      }
    },
    *fetchMembers({ payload }, { call, put, select }) {
      if (payload.id) {
        const members = yield call(lesson.listMembers, { lessonId: payload.id })
        const course = yield select(state => state.course.editing)

        yield put({
          type: 'save',
          payload: {
            editing: {
              ...course,
              members,
            },
          },
        })
      }
    },
    *reload(_, { put, select }) {
      const course = yield select(state => state.course.editing)
      yield put({
        type: 'fetch',
        payload: {
          id: course.id,
        },
      })
    },
    *publish({ payload }, { call, put }) {
      yield call(lesson.publish, { lessonId: payload.id, title: payload.title })
      yield put({ type: 'reload' })
      notification.success({
        message: '认证课程发布成功',
      })
    },
    *newSection({ payload }, { call, put, select }) {
      const course = yield select(state => state.course.editing)
      yield call(lesson.update, {
        lessonId: payload.id,
        sections: [...(course.sections || []), payload.section],
      })
      yield put({ type: 'reload' })
      message.success('课节添加成功')
    },
    *newQuestion({ payload }, { call, put, select }) {
      const course = yield select(state => state.course.editing)
      if (payload.question) {
        userInfo = localStore.get('userInfo') || {}
        notify({
          content: `用户 ${userInfo.id || userInfo.nickname} 在您的《${
            course.title
          }》课程中向您的提问`,
          from: userInfo.id,
          to: course.creator,
        })
        yield call(lesson.update, {
          lessonId: course.id,
          questions: [
            ...(course.questions || []),
            {
              ...payload.question,
              creator: userInfo.nickname,
              creatorId: userInfo.id,
              creatorAvatar: userInfo.avatar,
            },
          ],
        })
      }
      yield put({ type: 'reload' })
      message.success('问题添加成功')
    },
    *updateQuestion({ payload }, { call, put, select }) {
      const course = yield select(state => state.course.editing)
      if (payload.question) {
        yield call(lesson.update, {
          lessonId: course.id,
          questions: course.questions.map(q =>
            q.content === payload.question.content ? payload.question : q
          ),
        })
      }
      yield put({ type: 'reload' })
      message.success('答案添加成功')
    },
    *addTodo({ payload }, { call, put, select }) {
      const course = yield select(state => state.course.editing)
      yield call(lesson.update, {
        lessonId: course.id,
        tasks: [...(course.tasks || []), payload.todo],
      })
      yield put({ type: 'reload' })
      message.success('任务添加成功')
    },
    *like({ payload }, { call, put, select }) {
      yield call(lesson.like, {
        lessonId: payload.course.id,
        hash: getFakeHash(),
        likes: payload.course.likes || [],
        author: payload.course.creator,
        currentUser: userInfo.id,
        title: payload.course.title,
        abilityName: getFakeAbility(),
      })
      yield put({ type: 'reload' })
      message.success('认可成功，谢谢你的喜欢！')
    },
  },
  reducers: {
    save(state, { payload }) {
      return {
        ...state,
        ...payload,
      }
    },
    showModal(state) {
      return {
        ...state,
        error: {},
        modalVisible: true,
      }
    },
    showContractModal(state) {
      return {
        ...state,
        contractModalVisible: true,
      }
    },
    hideModal(state) {
      return {
        ...state,
        modalVisible: false,
      }
    },
    hideContractModal(state) {
      return {
        ...state,
        contractModalVisible: false,
      }
    },
  },
  subscriptions: {
    setup({ dispatch, history }) {
      return history.listen(({ pathname }) => {
        if (['/company/new-course'].includes(pathname)) {
          dispatch({ type: 'showModal' })
        }

        function fetchCourseById(path) {
          const match = pathToRegexp(path).exec(pathname)
          if (match) {
            dispatch({
              type: 'fetch',
              payload: { id: match[1] },
            })
            if (pathname.includes('company/course')) {
              dispatch({
                type: 'fetchMembers',
                payload: { id: match[1] },
              })
            }
          }
        }

        fetchCourseById('/company/edit-course/:id')
        fetchCourseById('/company/course/:id')
        fetchCourseById('/talent/course/:id')
      })
    },
  },
}
