import { Button } from 'antd'
import { random } from 'lodash'
import React from 'react'
import { Link } from 'dva/router'

export default function CourseStatus({ course }) {
  return (
    <Button.Group>
      <Button icon="usergroup-add">学员{course.members_count}</Button>
      <Button icon="like">认可{course.likes_count}</Button>
      <Button icon="form">评论{course.comments_count}</Button>
    </Button.Group>
  )
}

export function getCourseActions({ course, type, dispatch, tab }) {
  const like = async () => {
    await dispatch({ type: 'course/like', payload: { course } })
    if (type === 'talent') {
      dispatch({ type: 'home/fetch', payload: { type: tab } })
    } else {
      dispatch({ type: 'user/refreshUserInfo' })
    }
  }
  return [
    <Button icon="usergroup-add">
      <Link
        to={`/${type}/course/${course.id}${
          type === 'company' ? '?tab=students' : ''
        }`}
      >
        考生 {+course.members_count}
        {course.student_limit ? `/${course.student_limit}` : ''}
      </Link>
    </Button>,
    <Button icon="form">
      <Link
        to={`/${type}/course/${course.id}${
          type === 'company' ? '?tab=questions' : ''
        }`}
      >
        考证交流 {course.questions_count}
      </Link>
    </Button>,
    <Button icon="like" onClick={like}>
      认可度 {course.likes_count}
    </Button>,
    <Button icon="user-add">
      招聘需求 {course.jobs_count || random(3, 10)}
    </Button>,
  ]
}
