import { Button, Checkbox, Divider, Icon, Steps } from 'antd'
import React, { Component } from 'react'
import { fakeTasks } from '../fixtures/course'
import './TodoList.less'
const CheckboxGroup = Checkbox.Group
const Step = Steps.Step

export default class TodoList extends Component {
  state = {
    checkedList: [],
    indeterminate: true,
    checkAll: false,
  }

  render() {
    const { tasks = fakeTasks, type } = this.props
    const { indeterminate, checkAll, checkedList } = this.state
    return (
      <div className="TodoList">
        <div style={{ borderBottom: '1px solid #E9E9E9' }}>
          <Checkbox
            indeterminate={indeterminate}
            onChange={this.onCheckAllChange}
            checked={checkAll}
          >
            全部完成
          </Checkbox>
        </div>
        <br />
        <CheckboxGroup
          value={checkedList}
          onChange={this.onChange}
          style={{
            width: '100%',
          }}
        >
          {tasks.map((task, index) => (
            <div key={index} className="task_item">
              <Checkbox value={task} data-testid="course-task">
                {task}
              </Checkbox>
              {type === 'learn' && (
                <Button size="small">
                  <Icon type="upload" />
                  上传
                </Button>
              )}
            </div>
          ))}
        </CheckboxGroup>
        <Divider />
        <Steps
          size="small"
          current={checkedList.length === 0 ? 0 : indeterminate ? 1 : 2}
        >
          <Step title="等待中" />
          <Step title="进行中…" />
          <Step title="恭喜，已完成！" />
        </Steps>
      </div>
    )
  }

  onChange = checkedList => {
    this.setState({
      checkedList,
      indeterminate:
        !!checkedList.length && checkedList.length < this.props.tasks.length,
      checkAll: checkedList.length === this.props.tasks.length,
    })
  }
  onCheckAllChange = e => {
    this.setState({
      checkedList: e.target.checked ? this.props.tasks : [],
      indeterminate: false,
      checkAll: e.target.checked,
    })
  }
}
