import {
  Button,
  Checkbox,
  Divider,
  Modal,
  notification,
  message,
  Row,
  Steps,
} from 'antd'
import { connect } from 'dva'
import { routerRedux, Link } from 'dva/router'
import { get, isEmpty } from 'lodash'
import queryString from 'query-string'
import React, { Component } from 'react'
import ReactMarkdown from 'react-markdown'
import LocalStore from 'storejs'
import styled from 'styled-components'
import Comments from '../../components/Comments'
import TodoList from '../../components/TodoList'
import { buy } from '../../services/lesson'
import './course-detail.less'
import { Protocol } from '../../fixtures/policies'

const { Step } = Steps

const mapStateToProps = ({ course }) => ({ course })

class CourseDetail extends Component {
  state = {
    visibleBuyCourse: false,
    courseId: '',
    checkedValues: [],
  }
  getSectionContent = selected => {
    const { course } = this.props
    const section = get(course, `editing.sections.${selected}`, {})
    return (
      section.content || `### ${section.subtitle || course.editing.description}`
    )
  }
  renderSection = i => {
    const userInfo = LocalStore.get('userInfo') || {}
    const userId = userInfo.id
    const buyedCourse = LocalStore.get(`${userId}buyed_courses`) || []
    const disabled = !buyedCourse.includes(this.props.match.params.id) && i > 1
    if (disabled) {
      message.warn('请先购买课程')
      return
    }
    this.props.dispatch(routerRedux.push({ search: `?section=${i}` }))
  }
  agreePolicies = checkedValues => {
    this.setState({
      checkedValues,
    })
  }

  buyCourse() {
    this.setState({
      visibleBuyCourse: true,
    })
  }

  handleOk() {
    const userInfo = LocalStore.get('userInfo') || {}
    const userId = userInfo.id
    const lessonId = this.props.match.params.id
    buy({
      userId,
      lessonId,
    }).then(data => {
      const buyedCourse = LocalStore.get(`${userId}buyed_courses`) || []
      buyedCourse.push(lessonId)
      LocalStore.set(`${userId}buyed_courses`, Array.from(new Set(buyedCourse)))
      this.setState({
        visibleBuyCourse: false,
      })
      notification.success({
        message: '课程购买成功',
      })
    })
  }

  render() {
    const {
      course: { editing = {} },
      dispatch,
      location,
      match,
    } = this.props
    const parsed = queryString.parse(location.search)
    const selected = parsed.section || 0
    const userInfo = LocalStore.get('userInfo') || {}
    const buyedCourse = LocalStore.get(`${userInfo.id}buyed_courses`) || []
    return (
      <div className="course_detail">
        <div className="left">
          {buyedCourse.includes(match.params.id) ? (
            ''
          ) : (
            <Button
              type="primary"
              className="buy"
              onClick={this.buyCourse.bind(this)}
            >
              购买课程
            </Button>
          )}
          {isEmpty(editing.sections) ? (
            <div>
              <h3 style={{ textAlign: 'center' }}>🏆 站外课程认证</h3>
              <Divider />
              <Button type="primary" className="buy">
                <Link
                  to={editing.course_link}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  课程入口
                </Link>
              </Button>
              <Button type="primary" className="buy">
                <Link
                  to={editing.auth_link}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  认证入口
                </Link>
              </Button>
            </div>
          ) : (
            <Steps direction="vertical" current={+selected}>
              {editing.sections.map((s, i) => {
                const disabled = !buyedCourse.includes(match.params.id) && i > 1
                return (
                  <Step
                    style={{ cursor: 'pointer' }}
                    key={i}
                    disabled={disabled}
                    data-testid="course-section"
                    title={s.subtitle}
                    onClick={() => this.renderSection(i)}
                  />
                )
              })}
            </Steps>
          )}
        </div>
        <div className="center">
          <Title>{editing.title}</Title>
          <Background>
            <ContentBox>
              <ReactMarkdown source={this.getSectionContent(selected)} />
              <Row type="flex" justify="end">
                <Button
                  icon="like"
                  style={{
                    marginRight: '10px',
                  }}
                  onClick={() =>
                    dispatch({
                      type: 'course/like',
                      payload: { course: editing },
                    })
                  }
                >
                  认可 {editing.likes_count}
                </Button>
                <Button icon="form">请教 {editing.questions_count}</Button>
              </Row>
            </ContentBox>
            <br />
            <ContentBox>
              <Comments />
            </ContentBox>
          </Background>
        </div>
        <div className="right">
          <h3>✅ 认证任务</h3>
          <Divider />
          <TodoList tasks={editing.tasks} type="learn" />
        </div>
        {this.state.visibleBuyCourse ? (
          <Modal
            title="课程保护协议"
            okText="确认购买"
            okButtonProps={{ disabled: this.state.checkedValues.length !== 2 }}
            cancelText="我再看看"
            width={800}
            visible={this.state.visibleBuyCourse}
            onOk={this.handleOk.bind(this)}
            onCancel={() => this.setState({ visibleBuyCourse: false })}
          >
            <div>
              <Protocol />
            </div>
            <div className="course_buy_protocol">
              <Checkbox.Group
                value={this.state.checkedValues}
                onChange={this.agreePolicies}
              >
                <div>
                  <Checkbox value="agree1" data-testid="agree1">
                    同意支付<b>${editing.expense} Rea</b>币购买课程
                  </Checkbox>
                </div>
                <div>
                  <Checkbox value="agree2" data-testid="agree2">
                    我已仔细阅读课程购买协议
                  </Checkbox>
                </div>
                <span className="tip">
                  ! 若讲师未完成协议所承诺的，支付的Rea币将全额退款
                </span>
              </Checkbox.Group>
            </div>
          </Modal>
        ) : (
          ''
        )}
      </div>
    )
  }
}

const Title = styled.h2`
  text-align: center;
  vertical-align: middle;
  display: flex;
  align-items: center;
  /* text-decoration: underline; */
  justify-content: center;
  min-height: 50px;
  font-size: 24px;
`
const Background = styled.div`
  background: #f0f2f5;
  padding: 20px;
`

const ContentBox = styled.div`
  padding: 20px 30px;
  background: white;
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`

export default connect(mapStateToProps)(CourseDetail)
