import { Tabs } from 'antd'
import { connect } from 'dva'
import queryString from 'query-string'
import React, { Component } from 'react'
import { routerRedux } from 'dva/router'

import RankList from '../../components/RankList'
import StudentList from '../../components/StudentList'
import QuestionList from '../../components/QuestionList'
import TwoCol from '../../routers/TwoCol'

class TeachCourse extends Component {
  render() {
    const { dispatch, location, course, students } = this.props
    const parsed = queryString.parse(location.search)

    const handleTabChange = key => {
      dispatch(routerRedux.push({ search: `?tab=${key}` }))
      dispatch({
        type: 'home/fetchCourse',
        payload: {
          type: key,
        },
      })
    }
    return (
      <TwoCol
        left={
          <Tabs defaultActiveKey={parsed.tab} onChange={handleTabChange}>
            <Tabs.TabPane tab="在考学员" key="students">
              <StudentList
                lesson={course.editing}
                students={course.editing.members}
              />
            </Tabs.TabPane>
            <Tabs.TabPane tab="考证交流" key="questions">
              <QuestionList questions={course.editing.questions} />
            </Tabs.TabPane>
          </Tabs>
        }
        right={<RankList title="学员榜" type="student" dataSource={students} />}
      />
    )
  }
}

export default connect(({ course, home }) => ({
  course,
  students: home.students,
}))(TeachCourse)
