import { Alert, Form, Input, Modal } from 'antd'
import { connect } from 'dva'
import React from 'react'
import { contract } from '../fixtures/policies'
import { formItemLayout } from '../utils/form'

const courseFields = [{ name: '认证开课承诺书', field: 'contract' }]

const ContractModal = connect(({ course }) => ({ course }))(
  Form.create()(props => {
    const {
      course: { error, contractModalVisible },
      form,
      dispatch,
    } = props
    const { getFieldDecorator } = form

    const onCancel = () => {
      dispatch({ type: 'course/hideContractModal' })
    }

    const onSubmit = e => {
      e.preventDefault()
      form.validateFields((err, values) => {
        if (err) {
          return
        }
        /*
        dispatch({
          type: 'course/create',
          payload: values,
        })
        */
        dispatch({ type: 'course/hideContractModal' })
      })
    }

    return (
      <Modal
        visible={contractModalVisible}
        title="认证开课承诺书"
        onCancel={onCancel}
        onOk={onSubmit}
        width={'80vw'}
        height={'80vh'}
        cancelText="取消"
        okText="下一步"
        destroyOnClose
      >
        <Form {...formItemLayout}>
          {error && error.is && (
            <Alert
              style={{ marginTop: -15, marginBottom: 5 }}
              message={error.message}
              type="error"
              showIcon
            />
          )}
          {courseFields.map(item => (
            <Form.Item key={item.field}>
              {getFieldDecorator(item.field, {
                initialValue: contract,
                rules: [{ required: true, message: `${item.name}不能为空` }],
              })(
                <Input.TextArea
                  rows={30}
                  placeholder={`请输入${item.name}...`}
                  data-testid={'course-contract'}
                />
              )}
            </Form.Item>
          ))}
        </Form>
      </Modal>
    )
  })
)

export default ContractModal
