import {
  Avatar,
  Badge,
  Col,
  Divider,
  Icon,
  Layout,
  Menu,
  Row,
  Button,
  Popover,
  Modal,
} from 'antd'
import { Link, withRouter } from 'dva/router'
import { connect } from 'dva'
import React from 'react'
import { Helmet } from 'react-helmet'
import localStore from 'storejs'
import styled from 'styled-components'
import logoImg from '../assets/logo.png'
import Label from '../components/Label.js'
import './Header.css'

import Search from './Search'

const topics = [
  { name: '推荐', attention: false, co: 1031, li: 33 },
  { name: '后端', attention: false, co: 23423, li: 234 },
  { name: 'Android', attention: false, co: 344, li: 332 },
  { name: '测试', attention: false, co: 546, li: 56 },
  { name: 'iOS', attention: false, co: 554, li: 98 },
  { name: '产品', attention: false, co: 453, li: 278 },
  { name: '人工智能', attention: false, co: 85, li: 33 },
  { name: '区块链', attention: false, co: 45, li: 777 },
  { name: '大数据', attention: false, co: 787, li: 45667 },
]

class Header extends React.Component {
  state = {
    showLabel: false,
  }
  componentDidMount() {
    const userInfo = localStore.get('userInfo') || {}
    if (userInfo.type === 1) {
      const notFirst = localStore.get('notFirst')
      if (!notFirst) {
        localStore.set('notFirst', true)
        this.setState({ showLabel: true })
      }
    }
  }
  render() {
    const { isLightTheme = true, location, notification = {} } = this.props
    const { msgList = [] } = notification
    const userInfo = localStore.get('userInfo') || {}
    const msgListContent = (
      <div className="msg_list_content">
        {msgList.map((elem, index) => {
          return [<p key={index}>{elem.content}</p>, <Divider key="diver" />]
        })}
      </div>
    )

    const menus =
      userInfo.type === 1
        ? [
            { text: '首页', url: '/home' },
            { text: '在考认证', url: '/talent/courses' },
            { text: '关注企业', url: '/talent/companies' },
          ]
        : [
            { text: '开设认证', url: '/company/courses' },
            { text: '筛选人才', url: '/company/filter' },
            // migrate to Tabs (/company/attention)
          ]
    return (
      <Layout.Header
        className="header"
        data-testid="header"
        style={{
          height: 'auto',
        }}
      >
        <Helmet>
          <title>PandaHacker: 职场教育认证平台</title>
          <meta name="description" content="Helmet application" />
        </Helmet>
        <Row type="flex" align="middle">
          <Col span={8}>
            <Row type="flex" align="middle">
              <Col span={6}>
                <Link
                  to={userInfo.type === 1 ? '/home' : '/company/courses'}
                  className="title"
                  data-testid="header-logo"
                >
                  <img className="header_logo" src={logoImg} alt="" />
                </Link>
              </Col>
            </Row>
          </Col>
          <Col span={4} offset={1}>
            {userInfo.type === 2 ? '' : <Search />}
          </Col>
          <Col
            span={8}
            style={{
              textAlign: 'right',
            }}
          >
            <Menu
              theme={isLightTheme ? 'light' : 'dark'}
              mode="horizontal"
              selectedKeys={[location.pathname]}
              style={{ lineHeight: '64px', borderBottom: 'none' }}
            >
              {menus.map(m => (
                <Menu.Item key={m.url}>
                  <Link to={m.url}>「{m.text}」</Link>
                </Menu.Item>
              ))}
            </Menu>
          </Col>
          <Col span={1} style={{ textAlign: 'center' }}>
            <Popover
              placement="bottomRight"
              content={msgListContent}
              trigger="click"
            >
              <Badge count={msgList.length}>
                <Icon
                  type="bell"
                  style={{ fontSize: '20px', color: '#ea973d' }}
                />
              </Badge>
            </Popover>
          </Col>
          <Col span={1} style={{ textAlign: 'center' }}>
            <Link
              to={
                userInfo.type === 2
                  ? `/company/detail/${userInfo.id}`
                  : `/profile?id=${userInfo.id}`
              }
              data-testid="header-profile"
            >
              <Avatar src={userInfo.avatar} />
            </Link>
          </Col>
        </Row>
        {userInfo.type === 2 ? (
          ''
        ) : (
          <Row
            data-testid="header-topic"
            type="flex"
            justify="space-between"
            style={{
              borderTop: '1px solid #e8e8e8',
            }}
          >
            <StyledCol>
              {topics.map(t => (
                <Link
                  key={t.name}
                  to={`/home`}
                  style={{
                    marginRight: '34px',
                    color: t !== '推荐' && 'rgba(0, 0, 0, 0.65)',
                  }}
                >
                  {t.name}
                </Link>
              ))}
            </StyledCol>
            <StyledCol>
              <Button
                size="small"
                onClick={() => {
                  this.setState({ showLabel: true })
                }}
              >
                标签管理
              </Button>
            </StyledCol>
            <Modal
              title="关注行业"
              width={900}
              visible={this.state.showLabel}
              onCancel={() => {
                this.setState({ showLabel: false })
              }}
              onOk={() => {
                this.setState({ showLabel: false })
              }}
              okText="确定"
              cancelText="取消"
            >
              <Label data={topics} />
            </Modal>
          </Row>
        )}
      </Layout.Header>
    )
  }
}

const StyledCol = styled(Col)`
  height: 50px;
`

export default withRouter(
  connect(({ notification }) => ({
    notification,
  }))(Header)
)
