import { Button } from 'antd'
import { connect } from 'dva'

import { Link } from 'dva/router'
import * as PropTypes from 'prop-types'
import React from 'react'
import { Helmet } from 'react-helmet'
import Tour from 'reactour'
import localStore from 'storejs'
import TaughtList from '../../components/TaughtList'
import steps from '../../fixtures/steps'
import TwoCol from '../../routers/TwoCol'
import './courses.less'

class TeachList extends React.Component {
  state = {
    showTour: false,
  }

  componentDidMount() {
    const userInfo = localStore.get('userInfo') || {}
    if (userInfo.type === 2) {
      const notFirstForCompany = localStore.get('notFirstForCompany')
      if (!notFirstForCompany) {
        localStore.set('notFirstForCompany', true)
        this.setState({ showTour: true })
      }
    }
  }

  render() {
    const { dispatch, user } = this.props
    const { showTour } = this.state

    const userInfo = user.userInfo || localStore.get('userInfo') || {}
    return (
      <div className="main">
        <Helmet>
          <title>所开课程 - Reachain: 职场教育认证平台</title>
        </Helmet>
        <TwoCol
          left={<TaughtList courses={userInfo.taught} dispatch={dispatch} />}
          right={
            <Button type="primary" data-tour="new-course">
              <Link to="/company/new-course">新开认证</Link>
            </Button>
          }
        />
        <Tour
          steps={steps.company}
          isOpen={showTour}
          onRequestClose={() => this.setState({ showTour: false })}
        />
      </div>
    )
  }
}

TeachList.propTypes = {
  dispatch: PropTypes.any,
  user: PropTypes.any,
}

export default connect(({ user }) => ({ user }))(TeachList)
