export const createUserList = number => {
  const userList = [
    {
      nickname: '林奕君',
      position: 'Engineer',
      avatar: `https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1573314768347&di=71a317d02c0e4b588766a164eec05a9b&imgtype=0&src=http%3A%2F%2Fknsee.com%2Fuppics%2F201541122442425841.jpg`,
      abilities: [
        { name: '专业工程师（ACP）', creator: '阿里巴巴' },
        { name: '前端P6', creator: '阿里巴巴' },
        { name: '初级用户体验师', creator: '腾讯' },
        { name: '初级设计师', creator: '腾讯' },
      ],
      isAttention: true,
    },
  ]
  const positions = shuffle([
    '安全工程师',
    '前端设计师',
    'Javascript编程',
    '前端开发工程师',
    'WAP开发工程师',
    'PHP研发工程师',
    'ASP程序员',
    'JAVA程序员',
    'C#程序员',
    '数据库工程师',
    '搜索引擎',
    '架构设计师',
    'RIA开发人员',
    '系统工程师',
    '软件测试工程师',
    '客户端开发工程师',
    '数据挖掘工程师',
    '技术总监',
  ])
  for (let i = 0; i < number; i++) {
    const user = {
      nickname: shuffle(names)[i],
      position: positions[i],
      avatar: getFakeAvatar(),
      abilities: getFakeAbilities(),
      isAttention: Math.random() > 0.7,
    }
    userList.push(user)
  }
  return userList
}
