import { Button, Checkbox, Form, Icon, Input, message } from 'antd'
import { connect } from 'dva'
import { Link } from 'dva/router'
import React from 'react'
import logoImg from '../../assets/logo.png'
import './auth.less'

class NormalLoginForm extends React.Component {
  handleSubmit = e => {
    e.preventDefault()
    this.props.form.validateFields((err, values) => {
      if (!err) {
        const { dispatch } = this.props
        dispatch({
          type: 'user/login',
          payload: {
            username: values.username,
            password: values.password,
          },
        }).then(data => {
          if (!data) {
            message.error('用户名或密码错误！')
          } else {
            message.success('登陆成功，即将跳转到首页')
            this.props.history.push(
              data.type === 1 ? '/home' : '/company/filter'
            )
          }
        })
      }
    })
  }

  render() {
    const { getFieldDecorator } = this.props.form
    return (
      <div className="login_page">
        <img className="login_logo" src={logoImg} alt="" />
        <Form onSubmit={this.handleSubmit} className="login-form">
          <Form.Item>
            {getFieldDecorator('username', {
              rules: [{ required: true, message: '请输入用户名或邮箱账号' }],
            })(
              <Input
                prefix={
                  <Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />
                }
                placeholder="用户名或邮箱账号"
              />
            )}
          </Form.Item>
          <Form.Item>
            {getFieldDecorator('password', {
              rules: [{ required: true, message: '请输入密码!' }],
            })(
              <Input
                prefix={
                  <Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />
                }
                type="password"
                placeholder="密码"
              />
            )}
          </Form.Item>
          <Form.Item>
            {getFieldDecorator('remember', {
              valuePropName: 'checked',
              initialValue: true,
            })(<Checkbox>记住账号</Checkbox>)}
            <Link className="login-form-forgot" to="/login">
              忘记密码
            </Link>
            <Button
              type="primary"
              htmlType="submit"
              className="login-form-button"
            >
              登录
            </Button>
            <span>Or </span>
            <Link target="_self" to="/register">
              普通用户注册
            </Link>
            <Link target="_self" to="/coRegister" style={{ float: 'right' }}>
              企业注册
            </Link>
          </Form.Item>
        </Form>
      </div>
    )
  }
}
const LoginForm = Form.create({ name: 'normal_login' })(NormalLoginForm)
export default connect(({ user }) => ({
  user,
}))(LoginForm)
