import { routerRedux } from 'dva/router'
import queryString from 'query-string'
import localStore from 'storejs'
import * as api from '../services/user'

export default {
  namespace: 'user',
  state: {
    userInfo: {},
    profileUserInfo: {},
  },
  reducers: {
    updateState(state, action) {
      return { ...state, ...action.payload }
    },
  },
  effects: {
    *fetchUserInfo({ payload }, { call, put }) {
      const result = yield call(api.getUserInfoById, payload)
      if (result) {
        const currentUserInfo = localStore.get('userInfo') || {}
        if (payload.id === currentUserInfo.id) {
          yield put({
            type: 'updateState',
            payload: {
              userInfo: result,
            },
          })
          localStore.set('userInfo', result)
        }
        yield put({
          type: 'updateState',
          payload: {
            profileUserInfo: result,
          },
        })
      }
    },
    *refreshUserInfo(_, { put }) {
      const userInfo = localStore.get('userInfo') || {}
      yield put({
        type: 'fetchUserInfo',
        payload: {
          id: userInfo.id,
        },
      })
    },
    *login({ payload }, { call, put }) {
      const result = yield call(api.login, payload)
      if (result) {
        localStore.set('userInfo', result)
        yield put({
          type: 'updateState',
          payload: {
            userInfo: result,
          },
        })
        return result
      }
    },
  },
  subscriptions: {
    setup({ dispatch, history }) {
      history.listen(location => {
        if (/\/profile/.test(location.pathname)) {
          const parsed = queryString.parse(location.search)
          dispatch({
            type: 'fetchUserInfo',
            payload: {
              id: parsed.id,
            },
          })
        } else if (!['/login', '/register'].includes(location.pathname)) {
          const userInfo = localStore.get('userInfo') || {}
          if (!userInfo.type) {
            dispatch(routerRedux.push('/login'))
          } else {
            dispatch({ type: 'refreshUserInfo' })
          }
        }
      })
    },
  },
}
