import localStore from 'storejs'

import AV from './vendor/av-client'
import * as lessonInterface from './lesson'
import * as followInterface from './follow'
import { get_all_by_user_id } from './qualification'
import { getAll } from './abilities'

const USER_TABLE = 'users'

// 获取所有公司
export const companies = async () => {
  const query = new AV.Query(USER_TABLE)
  query.equalTo('type', 2)
  query.limit(5)
  const companies = await query.find()
  return companies.map(item => item.toFullJSON())
}

// 根据id获取单个公司
export const companyById = async ({ userId }) => {
  const query = new AV.Query(USER_TABLE)
  query.equalTo('objectId', userId)
  const company = await query.first()
  return company === undefined ? company : company.toFullJSON()
}

// 根据id修改公司
export const updateCompany = async ({
  userId,
  avatar,
  nickname,
  description,
}) => {
  const company = AV.Object.createWithoutData(USER_TABLE, userId)
  if (avatar) {
    company.set('avatar', avatar)
  }
  if (nickname) {
    company.set('nickname', nickname)
  }
  if (description) {
    company.set('description', description)
  }
  await company.save()
}

export const listUsers = async ({ userId }) => {
  const query = new AV.Query('users')

  const users = await query.find()

  const others = await Promise.all(
    users.filter(user => user.get('type') === 1).map(extractForCompany)
  )

  const followed = await filterFollowed(userId)
  return {
    users: others,
    followed_users: followed,
  }
}

export const popularTeachers = async () => {
  let query = new AV.Query('popular_teachers')
  const teachers = await query.find()

  return teachers.map(extractFieldsForPopular)
}

export const popularStudents = async () => {
  let query = new AV.Query('popular_students')
  const students = await query.find()

  return students.map(extractFieldsForPopular)
}

export const getUserInfoById = async ({ id }) => {
  let query = new AV.Query('users')
  const user = await query.get(id)

  return await extractFieldsForUser(user)
}

export const getSimpleUserInfoById = async ({ id }) => {
  let query = new AV.Query('users')
  const user = await query.get(id)

  return extractSimpleFieldsForUser(user)
}

export const login = async ({ username, password }) => {
  let query = new AV.Query('users')
  query.equalTo('username', username)
  query.equalTo('password', password)
  const user = await query.first()

  return extractFieldsForUser(user)
}

export const register = async ({
  username,
  nickname,
  password,
  type,
  avatar,
  corporate,
  description,
}) => {
  const UserObject = AV.Object.extend('users')
  const user = new UserObject()
  user.set('username', username)
  user.set('nickname', nickname)
  user.set('password', password)
  user.set('type', type) // 1->simple user, 2->company
  user.set('avatar', avatar)
  user.set('corporate', corporate)
  user.set('description', description)

  const saved = await user.save()
  return saved.id
}

export async function award({ userId, abilityName, stats }) {
  const query = new AV.Query('abilities')
  query.equalTo('user', userId)
  query.equalTo('name', abilityName)
  query.first().then(ability => {
    console.log('授予能力值')
    ability.increment('stats', stats)
    ability.save()
  })
}

export async function getUserName(userId) {
  if (userId) {
    let query = new AV.Query('users')
    query.select(['nickname'])
    const user = await query.get(userId)
    return user.get('nickname')
  }
  return ''
}

function extractFieldsForPopular(role) {
  const userInfo = localStore.get('userInfo') || {}

  return {
    id: userInfo.id, // fake current user
    name: role.get('name'),
    stats: role.get('stats'),
    position: role.get('position'),
    level: role.get('level'),
    avatar: role.get('avatar'),
  }
}

async function extractFieldsForUser(user) {
  if (user) {
    const taught = await lessonInterface.listTaught({ user: user.id })
    const approveNum = taught.reduce(function(prev, cur) {
      return prev + (cur.likes || []).length
    }, 0)
    return {
      id: user.id,
      avatar: user.get('avatar'),
      city: user.get('city'),
      nickname: user.get('nickname'),
      position: user.get('position'),
      type: user.get('type'),
      approve: approveNum,
      level: user.get('level'),
      learnt: await lessonInterface.listLearnt({ user: user.id }),
      taught: await lessonInterface.listTaught({ user: user.id }),
      abilities: await getAll(),
      qualifications: await get_all_by_user_id({ user_id: user.id }),
      following_companies: await getFollowingCompanies({ user: user.id }),
    }
  }

  return null
}

async function extractSimpleFieldsForUser(user) {
  if (user) {
    const abilities = []
    const abilityNum = abilities.reduce(function(prev, cur) {
      return prev + cur.stats
    }, 0)
    return {
      id: user.id,
      avatar: user.get('avatar'),
      city: user.get('city'),
      nickname: user.get('nickname'),
      position: user.get('position'),
      type: user.get('type'),
      approve: user.get('approve'),
      level: user.get('level'),
      taught: await lessonInterface.listTaught({ user: user.id }),
      learnt: await lessonInterface.listLearnt({ user: user.id }),
      abilities: [],
      abilityNum: abilityNum * 15,
    }
  }

  return null
}

async function extractForCompany(user) {
  if (user) {
    return {
      id: user.id,
      avatar: user.get('avatar'),
      city: user.get('city'),
      nickname: user.get('nickname'),
      position: user.get('position'),
      approve: user.get('approve'),
      level: user.get('level'),
      taught_count: user.get('taught_count'),
      abilities: [],
    }
  }

  return null
}

async function getFollowingCompanies({ user }) {
  return await followInterface.listFollowees({ user: user })
}

async function filterFollowed(campanyId) {
  const query = new AV.Query('follows')
  query.equalTo('follower', campanyId)
  const followers = await query.find()
  const idList = followers.map(follower => follower.get('followee'))
  return idList
}
