import { Button, Card, Col, Form, Input, Modal, Row } from 'antd'
import { connect } from 'dva'
import { Link } from 'dva/router'
import { get } from 'lodash'
import queryString from 'query-string'
import React, { useState } from 'react'
import ReactMarkdown from 'react-markdown'
import AddTodo from '../../components/AddTodo'
import ContractModal from '../../components/ContractModal'
import TodoList from '../../components/TodoList'

const mapStateToProps = ({ course }) => ({ course: course.editing })

export default connect(mapStateToProps)(Form.create()(EditCourse))

export function EditCourse({ form, course, match, location, dispatch }) {
  const [visible, setVisible] = useState(false)

  const { getFieldDecorator } = form
  const id = match.params.id

  return (
    <>
      <ContractModal />
      <Form>
        <Row type="flex" justify="space-between">
          <Col span={5}>
            <Card title="认证课题">
              <Form.Item>
                {getFieldDecorator('title', {
                  initialValue: course.title,
                  rules: [{ required: true, message: '课题不能为空' }],
                })(
                  <Input
                    placeholder="请输入课题..."
                    data-testid="edit-course"
                  />
                )}
              </Form.Item>
            </Card>
            <br />
            <Card title="课程内容">
              <Form.Item>
                {course.sections.map((s, i) => (
                  <div key={s.subtitle}>
                    <Link
                      to={`/company/edit-course/${id}?section=${i}`}
                      style={{
                        color: 'rgba(0, 0, 0, 0.65)',
                        fontWeight: 'bold',
                      }}
                    >
                      {s.subtitle}
                    </Link>
                  </div>
                ))}
                <Row type="flex">
                  <Col span={20}>
                    <Form.Item>
                      {getFieldDecorator('subtitle', {
                        initialValue: course.subtitle,
                        rules: [{ required: true, message: '内容不能为空' }],
                      })(
                        <Input
                          placeholder="请输入课节..."
                          data-testid="input-new-section"
                        />
                      )}
                    </Form.Item>
                  </Col>
                  <Col span={4}>
                    <Button
                      type="link"
                      onClick={handleNewSection}
                      data-testid="add-new-section"
                    >
                      新增
                    </Button>
                  </Col>
                </Row>
              </Form.Item>
            </Card>
          </Col>
          <Col span={13}>
            <Form.Item>
              <Row type="flex" justify="space-between">
                <Col>
                  <h2>{course.title}</h2>
                </Col>
                <Col>
                  <Button
                    onClick={previewText}
                    style={{
                      marginRight: '8px',
                    }}
                  >
                    预览
                  </Button>
                  <Button
                    type="primary"
                    onClick={publishCourse}
                    data-testid="publish-course"
                  >
                    发布
                  </Button>
                </Col>
              </Row>
              <Modal
                title="预览你的课程内容"
                visible={visible}
                onOk={hidePreview}
                onCancel={hidePreview}
              >
                <ReactMarkdown source={form.getFieldValue('content')} />
              </Modal>
            </Form.Item>
            <Form.Item>
              {getFieldDecorator('content', {
                initialValue: getEditingContent(),
                rules: [{ required: true, message: '内容不能为空' }],
              })(
                <Input.TextArea
                  rows={30}
                  data-testid="input-section-content"
                  className="textarea"
                  placeholder="😀 输入你的课程内容，支持 Markdown 格式哦…"
                  cols="50"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <h3>认证考试</h3>
            <Form.Item>
              <AddTodo />
            </Form.Item>
            <Form.Item>
              <TodoList tasks={course.tasks} />
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </>
  )

  async function handleNewSection() {
    const { getFieldValue, setFieldsValue } = form
    await dispatch({
      type: 'course/newSection',
      payload: {
        id,
        section: {
          subtitle: getFieldValue('subtitle'),
          content: getFieldValue('content'),
        },
      },
    })
    setFieldsValue({
      content: '',
      subtitle: '',
    })
  }

  function previewText(e) {
    e.preventDefault()
    setVisible(true)
  }

  function hidePreview() {
    setVisible(false)
  }

  function publishCourse() {
    const { getFieldValue } = form
    dispatch({
      type: 'course/publish',
      payload: {
        id,
        title: getFieldValue('title'),
      },
    })
  }

  function getEditingContent() {
    const parsed = queryString.parse(location.search)
    const selected = parsed.section || 0
    return get(course, `sections.${selected}.content`, '')
  }
}
