import { Button, Input } from 'antd'
import React, { Component } from 'react'
import './Label.less'
const { Search } = Input

class Label extends Component {
  state = {
    data: this.props.data.slice(1),
  }
  onChange(index) {
    let { data } = this.state
    data[index].attention = !data[index].attention
    this.setState({ data })
  }
  render() {
    return (
      <>
        <Search placeholder="搜索标签" style={{ width: 300, marginLeft: 6 }} />
        <div className="label">
          {this.state.data.map((item, index) => {
            return (
              <div className="label-box" key={index}>
                <div className="label-title">
                  <span>{item.name}</span>
                </div>
                <div className="meta-box">
                  <span>{item.co} 企业加入联盟</span>
                  <span>{item.li} 认证课程</span>
                </div>
                <Button
                  type="primary"
                  ghost={!item.attention}
                  onClick={() => {
                    this.onChange(index)
                  }}
                >
                  {item.attention ? '已关注' : '关注'}
                </Button>
              </div>
            )
          })}
        </div>
      </>
    )
  }
}

export default Label
