import { Avatar, Col, List, Progress, Row, Skeleton } from 'antd'
import { random } from 'lodash'
import React, { Component } from 'react'
import { Link } from 'dva/router'

class StudentList extends Component {
  state = {}
  render() {
    const { students, lesson } = this.props
    const renderStudentItem = item => {
      const learning = item.learnt.find(s => s.title === lesson.title)

      return (
        <List.Item data-testid={`student-item`}>
          <Skeleton avatar title={false} loading={false} active>
            <List.Item.Meta avatar={<Avatar src={item.avatar} />} />
            <Row type="flex" justify="space-between" align="middle">
              <Col span={4}>
                <div className="name">
                  <Link to={`/profile?id=${item.id || item.objectId}`}>
                    {item.nickname}
                  </Link>
                </div>
                <span> 能力值：{item.abilityNum}</span>
                <div>{item.position}</div>
              </Col>
              <Col span={14}>
                <div>
                  <span style={{ marginRight: 8 }}>
                    提问：{learning.questions_count}
                  </span>
                  <span>回答：{learning.answers_count}</span>
                </div>
                <div>
                  <span>学习进度：</span>
                  <Progress
                    strokeColor={{
                      '0%': '#108ee9',
                      '100%': '#87d068',
                    }}
                    width={350}
                    percent={item.learning_progress || random(20, 80)}
                  />
                </div>
                <div>
                  <span>任务进度：</span>
                  <Progress
                    strokeColor={{
                      '0%': '#108ee9',
                      '100%': '#87d068',
                    }}
                    width={350}
                    percent={item.learning_progress || random(20, 80)}
                  />
                </div>
              </Col>
              <Col span={4} offset={2}>
                <Progress
                  type="circle"
                  percent={75}
                  format={percent => (
                    <span style={{ fontSize: '14px' }}>评分：{percent} 分</span>
                  )}
                />
              </Col>
            </Row>
          </Skeleton>
        </List.Item>
      )
    }
    return (
      <List
        itemLayout="horizontal"
        dataSource={students}
        renderItem={renderStudentItem}
      />
    )
  }
}

export default StudentList
