import { Button, Checkbox, Col, Form, Input, message, Row } from 'antd'
import { Link } from 'dva/router'
import React from 'react'
import { register } from '../../services/user'
import logoImg from '../../assets/logo.png'
import './auth.less'

class RegistrationForm extends React.Component {
  state = {
    time: 60,
    confirmDirty: false,
  }

  getCaptcha = () => {
    this.countdown = setInterval(() => {
      const time = this.state.time - 1
      if (time < 0) {
        this.setState({ time: 60 })
        clearInterval(this.countdown)
        return
      }
      this.setState({ time })
    }, 1000)
  }

  componentWillUnmount() {
    clearInterval(this.countdown)
  }

  handleConfirmBlur = e => {
    const { value } = e.target
    this.setState({ confirmDirty: this.state.confirmDirty || !!value })
  }

  compareToFirstPassword = (rule, value, callback) => {
    const { form } = this.props
    if (value && value !== form.getFieldValue('password')) {
      callback('两次密码不一致!')
    } else {
      callback()
    }
  }

  handleSubmit = e => {
    e.preventDefault()
    this.props.form.validateFields((err, values) => {
      if (!err) {
        register({
          username: values.nickname,
          nickname: values.nickname,
          password: values.password,
          type: 1,
        }).then(data => {
          message.success('注册成功，即将跳转到登录页')
          setTimeout(() => {
            window.location.href = '/login'
          }, 2000)
        })
      }
    })
  }

  render() {
    const { getFieldDecorator } = this.props.form
    const { time } = this.state

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
      },
    }
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0,
        },
        sm: {
          span: 16,
          offset: 8,
        },
      },
    }

    return (
      <div className="register_page">
        <img className="login_logo" src={logoImg} alt="" />
        <Form
          {...formItemLayout}
          onSubmit={this.handleSubmit}
          className="register_form"
        >
          <Form.Item label={<span>昵称&nbsp;</span>}>
            {getFieldDecorator('nickname', {
              rules: [
                { required: true, message: '请输入昵称!', whitespace: true },
              ],
            })(<Input />)}
          </Form.Item>
          <Form.Item label="密码" hasFeedback>
            {getFieldDecorator('password', {
              rules: [
                {
                  required: true,
                  message: '请输入密码!',
                },
              ],
            })(<Input.Password />)}
          </Form.Item>
          <Form.Item label="确认密码" hasFeedback>
            {getFieldDecorator('confirm', {
              rules: [
                {
                  required: true,
                  message: '请再次输入密码!',
                },
                {
                  validator: this.compareToFirstPassword,
                },
              ],
            })(<Input.Password onBlur={this.handleConfirmBlur} />)}
          </Form.Item>
          <Form.Item label="邮箱">
            {getFieldDecorator('email', {
              rules: [
                {
                  type: 'email',
                  message: '请输入合法邮箱',
                },
                {
                  required: true,
                  message: '请输入邮箱地址',
                },
              ],
            })(<Input />)}
          </Form.Item>
          <Form.Item label="验证码">
            <Row gutter={8}>
              <Col span={12}>
                {getFieldDecorator('captcha', {
                  rules: [{ required: true, message: '请输入你获得的验证码!' }],
                })(<Input />)}
              </Col>
              <Col span={12}>
                <Button
                  className="get_captcha_btn"
                  disabled={time !== 60}
                  onClick={this.getCaptcha.bind(this)}
                >
                  {time === 60 ? '获取验证码' : time}
                </Button>
              </Col>
            </Row>
          </Form.Item>
          <Form.Item {...tailFormItemLayout}>
            {getFieldDecorator('agreement', {
              valuePropName: 'checked',
            })(
              <Checkbox>
                已经阅读 <Link to={'/policies'}>用户协议</Link>
              </Checkbox>
            )}
          </Form.Item>
          <Form.Item {...tailFormItemLayout}>
            <Button
              type="primary"
              htmlType="submit"
              className="register-form-button"
              onClick={this.handleSubmit}
            >
              注册
            </Button>
            Or{' '}
            <Link target="_self" to="/login">
              已有账号
            </Link>
          </Form.Item>
        </Form>
      </div>
    )
  }
}

export default Form.create({ name: 'register' })(RegistrationForm)
