import { Button, Form, Input } from 'antd'
import { connect } from 'dva'
import React from 'react'
import { reverse } from 'lodash'

import { fakeQuestions } from '../fixtures/course'
import CommentItem from './CommentItem'

const { TextArea } = Input

function Comments(props) {
  const {
    dispatch,
    form,
    course: { editing },
  } = props
  const handleSubmit = e => {
    e.preventDefault()
    form.validateFields((err, values) => {
      if (err) {
        return
      }

      dispatch({
        type: 'course/newQuestion',
        payload: {
          question: {
            content: values.question,
            selectedAnswer: null,
            createdAt: new Date(),
            answers: [],
          },
        },
      })

      form.setFieldsValue({
        question: '',
      })
    })
  }
  return (
    <Form onSubmit={handleSubmit}>
      <h3>🙋‍ 请教</h3>
      <h4>（在成长的同时帮助他人成长，在学习的同时帮助他人学习。）</h4>
      <Form.Item>
        {form.getFieldDecorator('question', {})(
          <TextArea
            rows={6}
            data-testid="input-question"
            className="textarea"
            placeholder="留言请教…"
            cols="50"
          />
        )}
      </Form.Item>
      <Form.Item style={{ textAlign: 'right' }}>
        <Button type="primary" htmlType="submit" data-testid="submit-question">
          发布
        </Button>
      </Form.Item>
      {reverse([...fakeQuestions, ...editing.questions]).map((q, i) => (
        <CommentItem key={i} question={q} />
      ))}
    </Form>
  )
}

export default connect(({ course }) => ({ course }))(Form.create()(Comments))
