import { Button, Card, Col, Dropdown, Icon, Menu, Progress, Row } from 'antd'
import { Link } from 'dva/router'

import React from 'react'
import { getFakeCertification } from '../fixtures/certification'
import { getCourseActions } from './CourseStatus'
import './TaughtList.less'

export default function TaughtList({ courses = [], dispatch }) {
  return courses.map(course => (
    <Card
      key={course.id}
      data-testid="course-item"
      title={
        <Link to={`/company/detail/${course.creatorObj.objectId}`}>
          {course.creatorObj.nickname}
        </Link>
      }
      style={{
        marginBottom: '20px',
      }}
      extra={
        <Dropdown
          overlay={
            <Menu>
              <Menu.Item>
                <Link to={`/company/edit-course/${course.id}`}>编辑</Link>
              </Menu.Item>
              <Menu.Item>
                <Link to={`/company/edit-course/${course.id}`}>删除</Link>
              </Menu.Item>
            </Menu>
          }
        >
          <Button>
            更多操作 <Icon type="down" />
          </Button>
        </Dropdown>
      }
      actions={getCourseActions({ course, type: 'company', dispatch })}
    >
      <Row type="flex" justify="space-between">
        <Col span={18}>
          <div>
            <h3>
              <Link
                to={`/company/course/${course.id}`}
                data-testid="learn-course-item"
              >
                {course.title}
              </Link>
            </h3>
          </div>
          <div>{course.description || getFakeCertification().description}</div>
          <div>
            认证周期：{course.start_time} ~ {course.end_time}
          </div>
          <Row
            type="flex"
            style={{
              flexFlow: 'nowrap',
            }}
          >
            <div style={{ minWidth: '80px' }}>通过率：</div>
            <Progress
              strokeColor={{
                '0%': '#108ee9',
                '100%': '#87d068',
              }}
              percent={course.teaching_progress}
            />
          </Row>
        </Col>
        <Col span={6} className="image-center">
          <img
            src={course.creatorObj.avatar}
            alt={''}
            width={150}
            style={{
              maxHeight: '185px',
            }}
          />
        </Col>
      </Row>
    </Card>
  ))
}
