import queryString from 'query-string'
import { createUserList, getTrueStudent } from '../fixtures/profile'
import lodash from 'lodash'
export default {
  namespace: 'company',
  state: {
    companyUser: {
      users: [],
      followed_users: [],
    },
  },
  subscriptions: {
    setup({ dispatch, history }) {
      dispatch({
        type: 'compannyGetUser',
      })
    },
  },
  reducers: {
    updateState(state, action) {
      return { ...state, ...action.payload }
    },
    shuffle(state, action) {
      const shuffleArr = JSON.parse(JSON.stringify(state.companyUser.users))
      return {
        ...state,
        companyUser: {
          ...state.companyUser,
          users: lodash.shuffle(shuffleArr),
        },
      }
    },
    compannyGetUser(state, action) {
      return {
        ...state,
        companyUser: {
          ...state.companyUser,
          users: createUserList(20),
        },
      }
    },
  },
  effects: {},
}
