import { BackTop, Layout } from 'antd'
import { Redirect, Route, routerRedux, Switch } from 'dva/router'
import React from 'react'
import App from './App'
import Header from '../components/Header'
import Login from '../pages/auth/login'
import Register from '../pages/auth/register'
import CoRegister from '../pages/auth/coRegister'
import coRegisterNext from '../pages/auth/coRegisterNext'

import Filter from '../pages/company/Filter'
import Courses from '../pages/company/courses'
import NewCourse from '../pages/company/new-course'
import EditCourse from '../pages/company/edit-course'
import CourseDetail from '../pages/company/teach-course'
import Block from '../pages/block'

import TalentHome from '../pages/talent'
import TalentCourses from '../pages/talent/courses'
import TalentCompany from '../pages/talent/companies'
import Company from '../pages/talent/company'
import TalentCourse from '../pages/talent/course-detail'

import Profile from '../pages/profile/Profile'

const { ConnectedRouter } = routerRedux
const { Content, Footer } = Layout

function routes({ history }) {
  return (
    <ConnectedRouter history={history}>
      <App>
        <Switch>
          <Redirect exact from="/" to="/home" />
          <Route path="/login" component={Login} />
          <Route path="/register" component={Register} />
          <Route path="/coRegister" component={CoRegister} />
          <Route path="/coRegisterNext" component={coRegisterNext} />
          <Layout>
            <Header />
            <Content style={{ padding: '20px 50px' }}>
              <Layout style={{ padding: '24px 0', background: '#fff' }}>
                <Content style={{ padding: '0 24px', minHeight: '90vh' }}>
                  <div className="container">
                    <Route path="/home" component={TalentHome} />
                    <Route path="/profile" component={Profile} />
                    <Route path="/block/:id" component={Block} />
                    <Route path="/talent/companies" component={TalentCompany} />
                    <Route path="/talent/courses" component={TalentCourses} />
                    <Route path="/talent/course/:id" component={TalentCourse} />

                    <Route path="/company/detail/:id" component={Company} />
                    <Route path="/company/courses" component={Courses} />
                    <Route path="/company/filter" component={Filter} />
                    <Route path="/company/new-course" component={NewCourse} />
                    <Route
                      path="/company/edit-course/:id"
                      component={EditCourse}
                    />
                    <Route
                      path="/company/course/:id"
                      component={CourseDetail}
                    />
                  </div>
                </Content>
              </Layout>
            </Content>
            <BackTop />
            <Footer style={{ textAlign: 'center' }}>
              ©2019 Created by PandaHacker.
            </Footer>
          </Layout>
        </Switch>
      </App>
    </ConnectedRouter>
  )
}

export default routes
