import { connect } from 'dva'
import React from 'react'
import LocalStore from 'storejs'

import RankList from '../../components/RankList'
import TwoCol from '../../routers/TwoCol'
import CourseList from './components/LearningCourseList'

export default connect(({ home }) => ({
  teachers: home.teachers,
}))(LearntCourses)

export function LearntCourses({ teachers = [] }) {
  const { learnt = [] } = LocalStore.get('userInfo') || {}
  return (
    <TwoCol
      left={<CourseList courses={learnt} />}
      right={<RankList title="热门企业" type="teacher" dataSource={teachers} />}
    />
  )
}
