import * as course from '../services/course'
import * as user from '../services/user'
import * as fetchQualification from '../services/qualification'

export default {
  namespace: 'home',
  state: {
    courses: [],
    teachers: [],
    companies: [],
    students: [],
    company: {},
  },
  effects: {
    *fetch({ payload }, { call, put }) {
      const courses = yield call(
        payload.type === 'popular' ? user.popularTeachers : course.latest
      )
      yield put({
        type: 'save',
        payload: {
          courses,
        },
      })
    },
    *fetchCompanies(_, { call, put }) {
      let companies = yield call(user.companies)
      for (let i = 0; i < companies.length; i++) {
        const qualification = yield fetchQualification.get_all_by_user_id({
          user_id: companies[i].objectId,
          type: 2,
        })
        companies[i].qualification = qualification
      }
      yield put({
        type: 'save',
        payload: {
          teachers: companies,
          companies: companies,
        },
      })
    },
    *fetchCompanyById({ payload }, { call, put }) {
      const company = yield call(user.getUserInfoById, payload)
      yield put({
        type: 'save',
        payload: {
          company,
        },
      })
    },
    *fetchStudents(_, { call, put }) {
      const students = yield call(user.popularStudents)
      yield put({
        type: 'save',
        payload: {
          students,
        },
      })
    },
  },
  reducers: {
    save(state, { payload }) {
      return {
        ...state,
        ...payload,
      }
    },
  },
  subscriptions: {
    setup({ dispatch, history }) {
      return history.listen(({ pathname }) => {
        if (['/home'].includes(pathname)) {
          dispatch({
            type: 'fetch',
            payload: {
              type: 'latest',
            },
          })
          dispatch({ type: 'fetchCompanies' })
        }
        if (pathname.includes('/talent/companies')) {
          dispatch({ type: 'fetchCompanies' })
        }
        if (pathname.includes('/company/course')) {
          dispatch({ type: 'fetchStudents' })
        }
      })
    },
  },
}
