import { isEmpty } from 'lodash'
import React from 'react'
import { Link } from 'dva/router'
import styled from 'styled-components'
import CourseItem from '../components/CourseItem'

export default function LearningCourseList({ courses }) {
  if (isEmpty(courses)) {
    return (
      <Center>
        <h1>
          <Link to="/home">课表为空，快去挑选您心仪的课程吧！</Link>
        </h1>
      </Center>
    )
  }

  return courses.map((item, index) => <CourseItem key={index} course={item} />)
}

const Center = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
`
