import AV from './vendor/av-client'
// import { notify } from './notification'
import { getUserName } from './user'

export async function follow({ currentUser, targetUser }) {
  const followClass = AV.Object.extend('follows')
  const follow = new followClass()

  follow.set('follower', currentUser)
  follow.set('followee', targetUser)

  follow.save().then(saved => {
    console.log('关注成功')
    /*notify({
      content: `${getUserName(currentUser)} 关注了你`,
      from: currentUser,
      to: targetUser,
    })*/
  })
}

export async function unfollow({ currentUser, targetUser }) {
  const query = new AV.Query('follows')

  query.equalTo('follower', currentUser)
  query.equalTo('followee', targetUser)

  query.first().then(follow => {
    const followed = AV.Object.createWithoutData('follows', follow.id)
    followed.destroy()
    console.log('取消关注成功')
  })
}

//获取用户关注的公司
export async function listFollowers({ user }) {
  const query = new AV.Query('follows')
  query.equalTo('follower', user)
  const followers = await query.find()
  const idList = followers.map(follower => follower.get('followee'))
  const listFollowers = []
  for (let i = 0; i < idList.length; i++) {
    listFollowers.push(await getUserName(idList[i]))
  }
  return listFollowers
}

//获取关注用户的公司
export async function listFollowees({ user }) {
  const query = new AV.Query('follows')
  query.equalTo('followee', user)
  const followees = await query.find()

  return await Promise.all(
    followees.map(followee => followee.get('follower')).map(getUserName)
  )
}
