import { Divider } from 'antd'
import { connect } from 'dva'
import React from 'react'
import CourseList from './components/LearningCourseList'

class CompanyItem extends React.Component {
  componentDidMount() {
    this.props.dispatch({
      type: 'home/fetchCompanyById',
      payload: { id: this.props.match.params.id },
    })
  }
  render() {
    const { company } = this.props
    return (
      <>
        {company.taught && (
          <div>
            <img
              src={company.avatar}
              alt={company.nickname}
              width={80}
              style={{ maxHeight: 80, marginRight: '20px' }}
            />
            <strong>{company.nickname}</strong>
            <span style={{ margin: '0 10px' }}>招聘需求：</span>
            <span style={{ margin: '0 10px' }}>专业工程师（ACP）8名</span>
            <span style={{ margin: '0 10px' }}>高级工程师（ACP）8名</span>
            <span> 架构师 4名</span>
          </div>
        )}
        <Divider key="Divider" />
        <CourseList courses={company.taught} />
      </>
    )
  }
}

export default connect(({ home }) => ({
  company: home.company,
}))(CompanyItem)
