import { Button, Card, Col, Row } from 'antd'
import { isEmpty, random } from 'lodash'
import React from 'react'
import { Link } from 'dva/router'
import styled from 'styled-components'

const fakeCompanies = [
  {
    companyID: 'aws',
    companyName: '亚马逊AWS',
    avatar: 'https://miro.medium.com/max/1200/1*tFl-8wQUENETYLjX5mYWuA.png',
    description:
      'AWS - 亚马逊公司旗下云计算服务平台,为世界各地的客户提供一整套基础设施和云解决方案. AWS面向用户提供整套云计算服务,能够帮助企业降低IT投入成本和维护...',
    category: '前端, 人工智能',
    courses_count: 10,
    proof_count: 98,
    student_count: 100,
    recruit_count: 2,
  },
]

export default function CompanyList({ companies = fakeCompanies, dispatch }) {
  if (isEmpty(companies)) {
    return (
      <Center>
        <h1>
          <Link to="/home">公司列表为空，快去关注您心仪的公司吧！</Link>
        </h1>
      </Center>
    )
  }

  return companies.map((company, index) => (
    <Card
      key={company.objectId}
      data-testid="course-item"
      style={{
        marginBottom: '20px',
      }}
      actions={[
        <Button icon="book">
          <Link to={`/company/detail/${company.objectId}`}>
            课程数量 {company.taught_count || 0}
          </Link>
        </Button>,
        <Button icon="trophy" onClick={() => {}}>
          认证通过 {company.student_count || 0}
        </Button>,
        <Button icon="usergroup-add">
          <Link to={`/company/detail/${company.objectId}`}>
            招聘人数 {company.recruit_count || 0}
          </Link>
        </Button>,
      ]}
    >
      <Row
        key={index}
        type="flex"
        style={{
          flexWrap: 'nowrap',
        }}
      >
        <Col>
          <img
            src={company.avatar}
            alt={company.nickname}
            width={80}
            style={{ maxHeight: 80, marginRight: '20px' }}
          />
          <strong>
            <Link to={`/company/detail/${company.objectId}`}>
              {company.nickname}
            </Link>
          </strong>
        </Col>
        <Col>
          <div
            style={{
              marginLeft: '20px',
              display: 'flex',
              height: '100%',
              justifyContent: 'center',
              flexDirection: 'column',
            }}
          >
            <div style={{ fontSize: '14px', color: '#333' }}>
              {company.description}
            </div>
            <div>
              <span>发布认证：</span>
              {company.qualification &&
                company.qualification.map(type => (
                  <span
                    style={{
                      background: '#eee',
                      color: '#ea973d',
                      padding: '3px 4px',
                      margin: '2px 4px',
                      fontWeight: 'bold',
                    }}
                    key={type.name}
                  >
                    {type.name}
                  </span>
                ))}
            </div>
          </div>
        </Col>
      </Row>
    </Card>
  ))
}

const Center = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
`
