import { DataView } from '@antv/data-set'
import G2 from '@antv/g2'

export function renderChart(
  data,
  {
    height = 400,
    padding = [20, 20, 50, 20],
    fields = ['近一个月', '近三个月', '近六个月'],
  }
) {
  const dv = new DataView().source(data)
  dv.transform({
    type: 'fold',
    fields: fields, // 展开字段集
    key: 'user', // key字段
    value: 'score', // value字段
  })
  const chart = new G2.Chart({
    container: 'mountNode',
    forceFit: true,
    height,
    padding,
  })
  chart.source(dv, {
    score: {
      min: 0,
      max: 2000,
    },
  })
  chart.coord('polar', {
    radius: 0.8,
  })
  chart.axis('item', {
    line: null,
    tickLine: null,
    grid: {
      lineStyle: {
        lineDash: null,
      },
      hideFirstLine: false,
    },
  })
  chart.axis('score', {
    line: null,
    tickLine: null,
    grid: {
      type: 'polygon',
      lineStyle: {
        lineDash: null,
      },
    },
  })
  chart.legend('user', {
    marker: 'circle',
    offset: 30,
  })
  chart
    .line()
    .position('item*score')
    .color('user')
    .size(2)
  chart
    .point()
    .position('item*score')
    .color('user')
    .shape('circle')
    .size(4)
    .style({
      stroke: '#fff',
      lineWidth: 1,
      fillOpacity: 1,
    })
  chart
    .area()
    .position('item*score')
    .color('user')
  chart.render()
}
