import AV from './vendor/av-client'

const QualificationTable = 'qualifications'

export const get_all_by_user_id = async ({ user_id, type }) => {
  if (type === 1) {
    console.log('List qualifications for simple user')
    const user = AV.Object.createWithoutData('users', user_id)
    const query = new AV.Query('user_qualification')
    query.equalTo('user', user)
    const user_qualifications = await query.find()
    return await Promise.all(
      user_qualifications.map(async user_qualification => {
        console.log(user_qualification)
        const qualification_id = user_qualification.get('qualification').id
        const passedAt = user_qualification.get('createdAt')
        console.log(qualification_id)
        const qualification = await get_qualification({
          qualification_id: qualification_id,
        })
        console.log(qualification)
        const response = qualification.toFullJSON()
        response.passedAt = passedAt
        return response
      })
    )
  }

  console.log('List qualifications for company')
  const user = AV.Object.createWithoutData('users', user_id)
  const query = new AV.Query(QualificationTable)
  query.equalTo('creator', user)
  const qualifications = await query.find()
  return qualifications.map(qualification => qualification.toFullJSON())
}

export const get_qualification = async ({ qualification_id }) => {
  const query = new AV.Query('qualifications')
  query.equalTo('objectId', qualification_id)
  return await query.first()
}

export const get_qualification_details = async ({
  user_id,
  qualification_id,
}) => {
  const userPointer = AV.Object.createWithoutData('users', user_id)
  const qualificationPointer = AV.Object.createWithoutData(
    'qualifications',
    qualification_id
  )
  const query = new AV.Query('user_qualification')
  query.equalTo('user', userPointer)
  query.equalTo('qualification', qualificationPointer)
  const detail = await query.first()
  return detail
}
