import { Avatar, Button, Comment, Form, Icon, Input, Tooltip } from 'antd'
import dayjs from 'dayjs'
import relativeTime from 'dayjs/plugin/relativeTime'
import { connect } from 'dva'
import { isEmpty } from 'lodash'
import { Link } from 'dva/router'
import React, { Component } from 'react'
import localStore from 'storejs'

const { TextArea } = Input

dayjs.extend(relativeTime)
const userInfo = localStore.get('userInfo') || {}

class CommentItem extends Component {
  state = {
    likes: 0,
    dislikes: 0,
    value: '',
    action: null,
    showEditor: false,
  }

  like = () => {
    this.setState(state => ({
      likes: state.likes + 1,
      // dislikes: 0,
      action: 'liked',
    }))
  }

  dislike = () => {
    this.setState(state => ({
      // likes: 0,
      dislikes: state.dislikes + 1,
      action: 'disliked',
    }))
  }
  showEditor = () => {
    this.setState(state => ({
      showEditor: !state.showEditor,
    }))
  }
  handleSubmit = e => {
    const { form, dispatch, question } = this.props
    e.preventDefault()
    form.validateFields(async (err, values) => {
      if (err) {
        return
      }

      await dispatch({
        type: 'course/updateQuestion',
        payload: {
          question: {
            ...question,
            answers: [
              ...question.answers,
              {
                creator: userInfo.nickname,
                creatorId: userInfo.id,
                creatorAvatar: userInfo.avatar,
                content: values.answer,
                createdAt: new Date(),
              },
            ],
          },
        },
      })

      form.setFieldsValue({
        answer: '',
      })
      this.setState({
        showEditor: false,
      })
    })
  }
  render() {
    const { likes, dislikes, action, showEditor } = this.state
    const {
      // course: { editing },
      question,
      form,
    } = this.props

    const getActions = showReply => [
      <span key="comment-basic-like">
        <Tooltip title="Like">
          <Icon
            type="like"
            theme={action === 'liked' ? 'filled' : 'outlined'}
            onClick={this.like}
          />
        </Tooltip>
        <span style={{ paddingLeft: 8, cursor: 'auto' }}>{likes}</span>
      </span>,
      <span key="comment-basic-dislike">
        <Tooltip title="Dislike">
          <Icon
            type="dislike"
            theme={action === 'disliked' ? 'filled' : 'outlined'}
            onClick={this.dislike}
          />
        </Tooltip>
        <span style={{ paddingLeft: 8, cursor: 'auto' }}>{dislikes}</span>
      </span>,
      showReply && (
        <span
          key="comment-basic-reply-to"
          data-testid="answer-question"
          onClick={this.showEditor}
        >
          👨‍🎓帮助
        </span>
      ),
    ]

    const Editor = () => (
      <div>
        <Form.Item data-testid="input-answer">
          {form.getFieldDecorator('answer', {
            rules: [{ required: true, message: '内容不能为空' }],
          })(<TextArea rows={4} />)}
        </Form.Item>
        <Form.Item style={{ textAlign: 'end' }}>
          <Button
            onClick={this.handleSubmit}
            type="primary"
            data-testid="submit-answer"
          >
            添加答案
          </Button>
        </Form.Item>
      </div>
    )

    const AExampleComment = ({ comment, children }) => (
      <Comment
        actions={getActions(comment.selectedAnswer === null)}
        author={
          <Link to={`/profile?id=${comment.creatorId}`}>{comment.creator}</Link>
        }
        avatar={<Avatar src={comment.creatorAvatar} alt={comment.creator} />}
        content={
          <div>
            <p>{comment.content}</p>
          </div>
        }
        datetime={
          <Tooltip
            title={dayjs(comment.createdAt).format('YYYY-MM-DD HH:mm:ss')}
          >
            <span>{dayjs(comment.createdAt).fromNow()}</span>
          </Tooltip>
        }
      >
        {comment.selectedAnswer === null && showEditor && <Editor />}
        {children}
      </Comment>
    )

    return (
      <AExampleComment comment={question}>
        {!isEmpty(question.answers) &&
          question.answers.map((a, i) => (
            <AExampleComment key={i} comment={a} />
          ))}
      </AExampleComment>
    )
  }
}

export default connect(({ course }) => ({ course }))(Form.create()(CommentItem))
