import { Avatar, Card, Col, Row } from 'antd'
import { random } from 'lodash'
import React from 'react'
import { Link } from 'dva/router'

import styled from 'styled-components'
import { getCourseActions } from './CourseStatus'

export default function CourseList({ courses = [], dispatch, tab }) {
  return courses.map((course, index) => (
    <Card
      key={course.id}
      data-testid="course-item"
      style={{
        marginBottom: '20px',
      }}
      title={
        <Row type="flex" align="middle">
          <Company>
            <Avatar
              src={course.creator_avatar || course.creator.avatar}
              alt={course.creator_nickname}
            />
            <Link to={`/company/detail/${course.creatorId || course.creator}`}>
              {course.creator_nickname || course.creator.nickname}
            </Link>
          </Company>
          {course.category &&
            course.category
              .split('，')
              .map(type => <Category key={type}>{type}</Category>)}
          <span
            style={{
              marginLeft: '6px',
              fontSize: '14px',
              color: 'rgba(0, 0, 0, 0.55)',
            }}
          >
            认证周期：{course.start_time} ~ {course.end_time}
          </span>
        </Row>
      }
      actions={getCourseActions({ course, type: 'talent', dispatch, tab })}
    >
      <Row key={index} type="flex" justify="space-between">
        <Col>
          <div>
            <h3>
              <Link
                to={`/talent/course/${course.id}`}
                data-testid="learn-course-item"
              >
                {course.title}
              </Link>
            </h3>
          </div>
          <div>{course.description}</div>
        </Col>
        <Col>
          <img
            src={course.avatar}
            alt={course.creator}
            width={150}
            style={{ maxHeight: 185 }}
          />
        </Col>
      </Row>
    </Card>
  ))
}

const Category = styled.span`
  margin: 0 15px;
  color: rgba(0, 0, 0, 0.65);
  font-size: 14px;
`

const Company = styled.strong`
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 2px;
  span {
    margin-right: 10px;
  }
`
