import AV from './vendor/av-client'

const NOTIFICATION_CLASS = 'notifications'

export const notify = async ({ content, from, to }) => {
  const NotificationObj = AV.Object.extend(NOTIFICATION_CLASS)
  const notification = new NotificationObj()
  notification.set('content', content)
  notification.set('from', from)
  notification.set('to', to)

  return await notification.save()
}

export const subscribe = async ({ table, event, func }) => {
  var query = new AV.Query(table)
  query.subscribe().then(function(liveQuery) {
    console.log(`成功订阅${table}的${event}事件`)
    liveQuery.on(event, func)
  })
}
