import { random } from 'lodash'
import React, { Component } from 'react'
import './Profile.less'
import { renderChart } from '../../utils/chart'

class RadarMap extends Component {
  constructor() {
    super()
    this.state = {}
  }

  componentDidMount() {
    const { abilities } = this.props
    const data = this.formatData(abilities)
    renderChart(data, {
      fields: ['近一个月', '近三个月', '近六个月'],
    })
  }

  formatData(abilities) {
    return abilities.map((ele, index) => ({
      id: index,
      item: ele.name,
      近一个月: ele.stats * 20 + random(1, 30),
      近三个月: ele.stats * 15 + random(1, 50),
      近六个月: ele.stats * 13 + random(1, 20),
    }))
  }

  render() {
    return <div id="mountNode"></div>
  }
}

export default RadarMap
