import React, { Component } from 'react'
import { Divider, Checkbox, Button, Input, Modal, message } from 'antd'
import { Link } from 'dva/router'
import './company.less'

class Block extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  render() {
    const { custPosition, data } = this.props
    const { abilities = [] } = data
    return (
      <div className="user_list" data-testid="recommend-user">
        <div key={'user_item'} className="user_item">
          <Link
            to="/profile?id=5dc7cf5ef4f71d0008b69281"
            className="user_name"
            data-testid="user-profile"
          >
            <img src={data.avatar} alt="" />
            <div>
              <p className="name">{data.nickname}</p>
              <p className="position">
                {custPosition ? custPosition : data.position}
              </p>
            </div>
          </Link>
          <div className="ability">
            {abilities.map((ele, index) => {
              return (
                <div key={index} className="ability_item">
                  <span className="name">{ele.name}：</span>
                  <span>{ele.creator}</span>
                </div>
              )
            })}
          </div>
          <div className="action">
            <Checkbox checked={data.isAttention}>关注他/她</Checkbox>
            <Button onClick={() => this.setState({ visibleMessage: true })}>
              留言
            </Button>
            {this.state.visibleMessage ? (
              <Modal
                title="给他留言"
                okText="确定"
                onOk={() => {
                  message.success('留言成功')
                  this.setState({
                    visibleMessage: false,
                  })
                }}
                cancelText="取消"
                bodyStyle={{
                  height: 100,
                }}
                visible={this.state.visibleMessage}
                onCancel={() => this.setState({ visibleMessage: false })}
              >
                <Input />
              </Modal>
            ) : (
              ''
            )}
          </div>
        </div>
        <Divider key="divider" />
      </div>
    )
  }
}

export default Block
