import { Button, Col, Divider, Modal, Row, Spin, Switch, Progress } from 'antd'
import { connect } from 'dva'
import React, { Component } from 'react'
import { Link } from 'dva/router'
import { uniq } from 'lodash'
import { format } from '../../utils/datetime'

import LocalStore from 'storejs'
import { fakeCompanies } from '../../fixtures/profile'
import TwoCol from '../../routers/TwoCol'
import { getActionsById } from '../../services/tracking'
import './Profile.less'
import RadarMap from './RadarMap'

class Profile extends Component {
  state = {
    visibleAbilityList: false,
    abilityList: [
      {
        content: `云江被吕靖认可，IOS能力获得提升`,
        index: 2,
        time: '2019-12-1 15:26',
        hash: '0..71651c4923f6a21aa287dfc5810031c36e9624fc03526',
      },
      {
        content: `获得95高分，IOS能力获得提升`,
        index: 3,
        time: '2019-12-1 15:26',
        hash: '0..71651c4923f6a21aa287dfc5810031c36e9624fc03526',
      },
      {
        content: `完成IOS课程，IOS能力获得提升`,
        index: 4,
        time: '2019-12-1 15:26',
        hash: '0..71651c4923f6a21aa287dfc5810031c36e9624fc03526',
      },
      {
        content: `完成HTML，Web能力获得提升`,
        index: 5,
        time: '2019-12-1 15:26',
        hash: '0..71651c4923f6a21aa287dfc5810031c36e9624fc03526',
      },
    ],
  }

  componentDidMount() {
    const userInfo = LocalStore.get('userInfo') || {}
    getActionsById({
      userId: userInfo.id,
    }).then(data => {
      this.setState({
        abilityList: data,
      })
    })
  }

  showAbilityList() {
    this.setState({
      visibleAbilityList: true,
    })
  }
  handleCancel() {
    this.setState({
      visibleAbilityList: false,
    })
  }
  onChange(checked) {
    console.log(`switch to ${checked}`)
  }
  render() {
    const { visibleAbilityList } = this.state
    const { profileUserInfo = {} } = this.props.user
    const currentUserInfo = LocalStore.get('userInfo') || {}
    const radarMapProps = {
      abilities: profileUserInfo.abilities,
    }
    return !profileUserInfo.id ? (
      ''
    ) : (
      <div>
        <TwoCol
          left={
            <div>
              <div className="user_info_head">
                <div className="left">
                  <img src={profileUserInfo.avatar} alt="" />
                  <div className="txt_des">
                    <p>
                      <b>
                        {profileUserInfo.nickname || profileUserInfo.username}
                      </b>
                    </p>
                    <p>
                      <span>{profileUserInfo.position}</span> |{' '}
                      <span>{profileUserInfo.city}</span>
                    </p>
                    <p>世之奇伟、瑰怪、非常之观、常在于险远</p>
                  </div>
                </div>
                {currentUserInfo.id === profileUserInfo.id ? (
                  <div className="user_info_edit">
                    <Button type="primary">编辑个人信息</Button>
                    <div>
                      <span>公开个人信息</span>
                      <Switch defaultChecked onChange={e => this.onChange(e)} />
                    </div>
                  </div>
                ) : (
                  ''
                )}
              </div>
              <Divider />
              <div className="chart">
                <RadarMap {...radarMapProps} />
                <div className="bar">
                  {profileUserInfo.abilities &&
                    profileUserInfo.abilities.map((ele, index) => {
                      return (
                        <div
                          key={index}
                          data-testid="ability-bar"
                          className="ability_bar"
                          onClick={this.showAbilityList.bind(this)}
                        >
                          <span>
                            <b>{ele.name}:</b>
                          </span>
                          <Progress
                            strokeColor={{
                              '0%': '#f4cb9e',
                              '100%': '#ea973d',
                            }}
                            percent={(ele.stats * 15) / 20}
                            format={percent => ele.stats * 15}
                          />
                        </div>
                      )
                    })}
                </div>
              </div>
              <h2 style={{ fontWeight: 800, marginLeft: '2em', color: '#555' }}>
                证书获取记录
              </h2>
              {currentUserInfo.qualifications.map((ele, index) => {
                return (
                  <div
                    key={index}
                    className="uer_license"
                    onClick={this.showAbilityList.bind(this)}
                  >
                    <span>
                      <b>{ele.name}</b>
                    </span>
                    <span className="uer_license_time">
                      <b>{`${(ele.passedAt && ele.passedAt.substring(0, 10)) ||
                        format(new Date())} 通过 ${
                        ele.creator.nickname
                      } 获取`}</b>
                    </span>
                  </div>
                )
              })}
              <Divider />
              <div className="experience">
                <div>
                  <h3>
                    <b>工作经历</b>
                  </h3>
                  <p>
                    <span>Amazon 亚马逊</span>
                    <span>高级产品经理</span>
                    <span>2019.5-至今</span>
                  </p>
                  <p>
                    <span>Tencent 腾讯</span>
                    <span>产品经理</span>
                    <span>2017.6-2019.4</span>
                  </p>
                  <p>
                    <span>Baidu 百度</span>
                    <span>产品经理</span>
                    <span>2015.1-2017.5</span>
                  </p>
                </div>
                <div>
                  <h3>
                    <b>学习经历</b>
                  </h3>
                  <p>
                    <span>《Architecting on AWS – Accelerator》</span>
                    <span>AWS</span>
                  </p>
                  <p>
                    <span>《Migrating to the Cloud》</span>
                    <span>AWS</span>
                  </p>
                  <p>
                    <span>《云服务器基础运维与管理》</span>
                    <span>阿里巴巴</span>
                  </p>
                  <p>
                    <span>《根据业务量弹性使用云资源》</span>
                    <span>阿里巴巴</span>
                  </p>
                  <p>
                    <span>《大数据》</span>
                    <span>百度</span>
                  </p>
                </div>
              </div>
            </div>
          }
          right={
            <div className="profile_right">
              <div className="follow_company">
                <h3>
                  关注
                  {currentUserInfo.id === profileUserInfo.id ? '你的' : '他的'}
                  公司
                </h3>
                <Divider />
                {uniq([
                  ...profileUserInfo.following_companies,
                  ...fakeCompanies,
                ]).map((ele, index) => {
                  return (
                    <Link to={`/company/detail/${ele.id}`}>
                      <p key={index}>{ele.name}</p>
                    </Link>
                  )
                })}
              </div>
            </div>
          }
        />
        {visibleAbilityList ? (
          <Modal
            title="能力提升记录"
            visible={this.state.visibleAbilityList}
            footer={null}
            width={'60vw'}
            onCancel={this.handleCancel.bind(this)}
          >
            {this.state.abilityList.map((ele, index) => {
              return [
                <div
                  className="block"
                  key={index}
                  style={{
                    margin: '12px 0',
                  }}
                >
                  <Row type="flex" justify="space-between">
                    <Col>
                      {ele.hash ? (
                        <Link
                          target="_blank"
                          to={`/block/${ele.hash}`}
                          data-testid="block-link"
                          rel="noopener noreferrer"
                        >
                          {ele.hash}
                        </Link>
                      ) : (
                        <span
                          className="not_on_chain"
                          style={{
                            color: '#f4cb9e',
                          }}
                        >
                          等待上链
                        </span>
                      )}
                    </Col>
                    <Col>
                      <strong>{ele.time}</strong>
                    </Col>
                  </Row>

                  <div>{ele.content}</div>
                </div>,
                <Divider key={`hr${index}`} />,
              ]
            })}
            <div className="loading">
              <Spin size="small" /> 加载更多...
            </div>
          </Modal>
        ) : (
          ''
        )}
      </div>
    )
  }
}

export default connect(({ user }) => ({
  user,
}))(Profile)
