import { Col, Row, Layout } from 'antd'
import React from 'react'

export default function TwoCol({ left, right, inverse }) {
  const leftProps = inverse ? { span: 5 } : { span: 18 }
  const rightProps = inverse ? { span: 18, offset: 1 } : { span: 5, offset: 1 }
  return (
    <Row type="flex">
      <Col {...leftProps}>{left}</Col>
      <Col {...rightProps}>
        <Layout.Sider
          style={{ background: '#fff' }}
          width="100%"
          data-testid="popular-teacher"
        >
          {right}
        </Layout.Sider>
      </Col>
    </Row>
  )
}
